.section-download
    width: 100%
    max-width: 1060px
    margin-left: auto
    margin-right: auto
    background: $colorbluelight
    padding: 35px 80px
    display: flex
    align-items: center
    justify-content: space-between
    border-radius: 3px
    @include r(1023px)
        margin-left: -24px
        margin-right: -24px
        width: auto
        padding-left: 104px
        padding-right: 24px
        align-items: flex-start
    @include r(767px)
        display: block
        padding: 30px 16px
        margin-left: auto
        margin-right: auto
        max-width: 545px
        text-align: center
    @include r(545px)
        margin-left: -16px
        margin-right: -16px

.section-download__content
    @include r(767px)
        margin-bottom: 40px
        
.section-download__title
    font-weight: 500
    margin-bottom: 20px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none

.section-download__descr
    width: 100%
    max-width: 485px
    margin-bottom: 40px
    opacity: 0
    transform: translate(0, 30%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.5s $transitiondefault
    transition-delay: 0.3s
    .is-animated &
        opacity: 1
        transform: none
    @include r(767px)
        margin-bottom: 30px

.section-download__btn
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.7s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
        transform: none

.section-download__img
    margin: 0 50px
    flex: none
    display: block
    box-shadow: 6px 6px 12px rgba(76, 108, 125, 0.17), -3px 0px 12px rgba(76, 108, 125, 0.13)
    @include r(1023px)
        margin-right: 0
        width: 200px
    @include r(767px)
        margin: 0 auto