.section-leader
    padding-top: 40px
    @include r(767px)
        padding-top: 0
    
.section-leader__container
    display: flex
    @include r(767px)
        display: block

.section-leader__left
    flex: none
    width: calc(50% + 15px)
    padding-right: 50px
    @include r(1399px)
        width: calc(50% - 50px)
    @include r(1023px)
        width: 50%
    @include r(767px)
        width: auto
        padding-right: 0
        margin-bottom: 30px

.section-leader__right
    flex: none
    width: calc(50% - 15px)
    max-width: 530px
    @include r(1399px)
        width: calc(50% + 50px)
    @include r(1023px)
        width: 50%
    @include r(767px)
        width: auto
    
.section-leader__img-wrap
    width: 436px
    position: relative
    &::before
        content: ""
        display: block
        position: absolute
        width: 73%
        height: 65%
        background: $colorbggrey
        right: -13%
        top: -13%
        opacity: 0
        transform: translateX(-100vw)
        transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
        transition-delay: 0.6s
    .is-animated &::before
        opacity: 1
        transform: none
    @include r(1399px)
        width: 80%
    @include r(767px)
        width: 100%
        max-width: 400px
        margin: 0 auto
        &::before
            content: none
    @include r(431px)
        margin: 0 -16px
        width: auto
        max-width: none

.section-leader__img
    display: block
    width: 100%
    height: auto
    position: relative
    z-index: 1

.section-leader__title
    margin-bottom: 60px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    .is-animated &
        opacity: 1
        transform: none
    @include r(1399px)
        margin-bottom: 40px
    @include r(1023px)
        margin-bottom: 20px

.section-leader__descr
    font-size: 24px
    line-height: 34px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        font-size: 22px
        line-height: 30px
    @include r(767px)
        font-size: 18px
        line-height: 28px

.section-leader__txt
    margin-bottom: 28px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.2s
    .is-animated &
        opacity: 1
        transform: none

.section-leader__name
    color: $colorblue
    font-weight: bold
    font-family: $fontmont
    margin-bottom: 8px
    opacity: 0
    transition: opacity 1.5s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
        transform: none

.section-leader__pos
    color: $colorblue
    font-size: 16px
    line-height: 20px
    font-family: $fontmont
    opacity: 0
    transition: opacity 1.5s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
        transform: none
    @include r(767px)
        font-size: 14px
        line-height: 20px