.section-intro
    padding: 95px 80px
    margin-right: -80px
    display: flex
    align-items: flex-end
    position: relative
    &::before
        content: ''
        display: block
        width: 100%
        height: 100%
        background: $colorbggrey
        position: absolute
        right: 0
        top: 0
        opacity: 0
        transform: translateX(100vw)
        transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    &.is-animated::before
        opacity: 1
        transform: none
    @include r(1399px)
        margin-right: -30px
        margin-left: -30px
    @include r(1199px)
        padding: 50px 30px
    @include r(1023px)
        display: block
        margin-right: -24px
        margin-left: -24px
    @include r(767px)
        margin-right: -16px
        margin-left: -16px
        padding: 30px 16px 0

.section-intro__content
    width: 390px
    margin-right: 75px
    position: relative
    @include r(1399px)
        flex-grow: 1
    @include r(1023px)
        width: auto
        margin-right: 0
        margin-bottom: 50px
    @include r(767px)
        margin-bottom: 30px

.section-intro__img-box
    width: 625px
    height: 560px
    flex: none
    margin-top: -200px
    position: relative
    overflow: hidden
    @include r(1399px)
        width: 50%
        margin-right: -80px
    @include r(1199px)
        margin-top: -150px
        margin-right: -30px
    @include r(1023px)
        margin-right: 0
        margin-top: 0
        width: 100%
        height: auto
    @include r(767px)
        width: auto
        margin-left: -16px
        margin-right: -16px

.section-intro__img
    width: 100%
    height: 100%
    display: block
    object-fit: cover
    opacity: 0
    transform: scale(1.2)
    transition: opacity 1.5s $transitiondefault, transform 2.8s linear
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        height: auto
        object-fit: contain

.section-intro__title
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1s
    .is-animated &
        opacity: 1
        transform: none

.section-intro__descr
    font-size: 24px
    line-height: 34px
    font-family: $fontmont
    font-weight: 600
    width: 100%
    max-width: 550px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px

.section-intro__txt
    opacity: 0
    transform: translate(0, 40%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 2s $transitiondefault
    transition-delay: 1.4s
    .is-animated &
        opacity: 1
        transform: none
