// fonts
$fontdefault: 'calibri', Arial, sans-serif
$fontbody: normal normal 18px/28px $fontdefault
$fontmont: 'montserrat', Arial, sans-serif

// colors
$colortxt: #000
$colortxtgrey: #5E5E5E
$colortxtgreylight: #777
$colortextgrey: #606060
$colorbggrey: #F5F5F5
$colorblue: #00A4F4
$colorbluelight: #EAF8FF
$colorgreen: #55D102
$colorgreenlight: #ddf6cc
$colorborder: #C4C4C4
$colorerror: #FF5823

// transition
$transitiondefault: cubic-bezier(0.4, 0, 0, 1)

// animations
@keyframes elementWidth
    0%
        width: 0
    100%
        width: 100%

@keyframes bounce
    0%
        transform: scale(1, 1) translateY(0)
    10%
        transform: scale(1.1, 0.9) translateY(0)
    30%
        transform: scale(0.9, 1.1) translateY(-70%)
    50%
        transform: scale(1.05, 0.95) translateY(0)
    57%
        transform: scale(1, 1) translateY(-10%)
    64%
        transform: scale(1, 1) translateY(0)
    100%
        transform: scale(1, 1) translateY(0)
