.section-reviews
    @include r(1023px)
        padding-top: 120px
        margin-top: -40px

.section-reviews__title
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
        transform: none

.section-reviews__subtitle
    font-weight: 500
    font-family: $fontmont
    color: $colortxtgrey
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.3s $transitiondefault
    transition-delay: 0.7s
    .is-animated &
        opacity: 1
        transform: none

.section-reviews__slider
    opacity: 0
    transition: opacity 1.5s $transitiondefault
    transition-delay: 1s
    .is-animated &
        opacity: 1
        transform: none
