.leader
    display: flex
    flex-wrap: wrap
    justify-content: space-around
    margin: 0
    padding: 0
    list-style: none
    @include r(767px)
        display: block

.leader__item
    width: 50%
    flex: none
    position: relative
    padding-bottom: 80px
    &::before
        content: ''
        display: block
        position: absolute
        left: 0
        right: -80px
        background: $colorblue
        top: 100px
        bottom: 0
        opacity: 0
        transition: opacity 0.6s linear, top 1.5s $transitiondefault
    &:nth-child(odd)
        &::before
            left: -80px
            right: 0
    &:nth-child(even)
        &::before
            left: 0
            right: -80px
    &.is-active
        color: white
        &::before
            opacity: 1
            top: 160px
        .leader__name
            color: white !important
        .leader__more
            color: white
            &::before
                background: none
                height: 2px
                background: white
                top: 0.55em
        .leader__content
            &::before
                height: calc(100% - 5px)

    @include r(1399px)
        &:nth-child(odd)
            &::before
                left: -30px
        &:nth-child(even)
            &::before
                right: -30px

    @include r(1199px)
        &::before
            top: 100px

    @include r(1023px)
        &:nth-child(odd)
            &::before
                left: -24px
        &:nth-child(even)
            &::before
                left: 0

    @include r(767px)
        width: auto
        margin-bottom: 50px
        padding-bottom: 40px
        &::before
            top: 100px
        &:nth-child(odd)
            &::before
                left: -16px
                right: -16px
        &:nth-child(even)
            &::before
                left: -16px
                right: -16px
        &:last-child
            margin-bottom: 0

.leader__card
    display: block
    text-decoration: none
    width: 436px
    margin: 0 auto
    position: relative
    &:hover
        .leader__name
            color: $colorblue
        .leader__more::after
            width: 100%
    @include r(1199px)
        width: 350px
    @include r(1023px)
        width: 290px

.leader__img
    display: block
    width: 100%
    height: 520px
    object-fit: cover
    object-position: top
    margin-bottom: 30px
    @include r(1199px)
        height: 400px
    @include r(1023px)
        height: 340px

.leader__name
    font: bold 24px/28px $fontmont
    margin-bottom: 15px
    transition: 0.2s

.leader__pos
    font-family: $fontmont
    font-weight: 500
    margin-bottom: 25px

.leader__content
    position: relative
    margin-top: 35px
    display: none
    font-size: 16px
    line-height: 24px
    &::before
        content: ''
        display: block
        width: 1px
        background: white
        position: absolute
        left: -30px
        top: 5px
        height: 0
        transition: 0.4s
        transition-delay: 0.3s
    @include r(1023px)
        &::before
            left: -17px
