.section-next
    text-align: center

.section-next__subtitle
    font-weight: bold
    color: $colorblue
    margin-bottom: 10px

.section-next__pagi
    width: 100%
    max-width: 840px
    margin: 0 auto
    padding: 0 100px
    position: relative

.section-next__pagi-prev,
.section-next__pagi-next
    display: block
    width: 60px
    height: 60px
    border: 1px solid transparent
    background: url(../images/arrow-blue.svg) no-repeat center
    transition: 0.2s
    position: absolute
    top: 50%
    transform: translateY(-50%)
    &:hover
        border-color: $colorblue

.section-next__pagi-prev
    transform: translateY(-50%) rotate(180deg)
    left: 0

.section-next__pagi-next
    right: 0

.section-next__title
    margin-bottom: 0
    width: 100%
    max-width: 624px