.solutions__navbar {
    height: 58px;
    margin: -100px auto 50px;
    @include r(1199px) {
        height: 83px;
    }
    @include r(1023px) {
        margin-top: -55px;
    }
    @include r(767px) {
        height: 48px;
        margin-top: -20px;
        margin-bottom: 30px;
    }
}

.navigation__fixed {
    position: fixed;
    top: 90px;
    left: 0;
    z-index: 900;
    width: 100%;
    height: 58px;
    @include r(1199px) {
        height: 73px;
    }
    @include r(1023px) {
        top: 70px;
    }
    @include r(767px) {
        height: 48px;
    }
}

.navigation__list {
    max-width: 2560px;
    height: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
    margin: 0;
    margin-bottom: 50px;
    list-style-type: none;
    background-color: white;
}

.career_navigation__list {
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 0;
    margin: 0;
    margin-bottom: 50px;
    list-style-type: none;
    background-color: white;
}

.navigation__item {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
    margin: 0;
    border-bottom: 1px solid $colorborder;
    font-family: $fontmont;
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
    color: $colortxtgreylight;

    &.active {
        background-color: $colorbluelight;
        border-bottom: 2px solid $colorblue;
        font-weight: 700;
        color: $colorblue;
    }

    @include r(1199px) {
        padding: 10px;
    }

    @include r(767px) {
        padding: 10px;
        font-size: 14px;
        line-height: 20px;
    }

    @include r(575px) {
        padding: 5px 7px;
        font-size: 14px;
        line-height: 18px;
    }
}

.navigation__text-wide {
    display: block;
    @include r(575px) {
        display: none;
    }
}

.navigation__text-small {
    display: none;
    @include r(575px) {
        display: block;
    }
}

.navigation__link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 5;
}

.solutions__description {
    position: relative;
    z-index: 2;
}

.description__wrapper {
    display: flex;
    gap: 0 10.94%;

    @include r(1023px) {
        gap: 0 6%;
    }

    @include r(767px) {
        flex-wrap: wrap;
    }
}

.description__image {
    width: 42.58%;

    img {
        display: block;
        width: 100%;
        height: auto;
    }

    @include r(1023px) {
        width: 42%;
    }

    @include r(767px) {
        width: 100%;
        order: 2;

        img {
            width: calc(100% + 32px);
            margin-left: -16px;
            max-height: 500px;
            object-fit: cover;
        }
    }
}

.description__content {
    width: 46.48%;

    @include r(1023px) {
        width: 52%;
    }

    @include r(767px) {
        width: 100%;
        order: 1;
    }
}

.description__title {
    margin-bottom: 20px;

    @include r(767px) {
        margin: 0 auto 15px;
        font-size: 20px;
        line-height: 30px;
    }
}

.description__title.section-general__title {
    margin-bottom: 20px;

    @include r(767px) {
        margin: 0 auto 20px;
        text-align: center;
    }
}

.description__subtitle {
    font-weight: 700;
    font-family: $fontmont;

    @include r(767px) {
        max-width: 520px;
        margin-bottom: 20px;
    }
}

.description__text {
    @include r(767px) {
        max-width: 600px;
        margin-bottom: 20px;
    }
}

///////////

.solutions__info {
    position: relative;
    margin-top: -50px;
    padding-top: 110px;
    overflow: hidden;

    @include r(1023px) {
        margin-top: 30px;
        padding-top: 90px;
    }
}

.info__bg {
    position: absolute;
    top: 0;
    left: 50%;
    z-index: -1;
    transform: translateX(-50%);
    width: 133%;
    min-width: 800px;
    background-color: $colorbluelight;
    border-radius: 50%;

    &::before {
        content: "";
        display: block;
        padding-top: 100%;
    }

    &::after {
        content: "";
        display: block;
        background-color: $colorbluelight;
        border-radius: 50%;
    }
}

.solutions__container {
    background-color: $colorbluelight;
}

.info__icon {
    width: 165px;
    margin: 0 auto 30px;

    span {
        display: block;
        width: 100%;
        height: auto;
    }

    @include r(767px) {
        width: 118px;
    }
}

.info__descr {
    max-width: 684px;
    margin: 0 auto 20px;
    font-family: $fontmont;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    color: $colortxt;
    text-align: center;

    @include r(767px) {
        font-size: 18px;
        line-height: 28px;
    }
}

.info__text {
    max-width: 664px;
    margin: 0 auto 70px;
    text-align: center;

    // @include r(767px) {
    //     text-align: center;
    // }
}

.info__btn {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.info__details {
    background-color: $colorbluelight;
}

.info__wrapper {
    position: relative;
    top: 10px;
    width: 82.81%;
    display: grid;
    grid-template-columns: 61.93% 30%;
    gap: 0 8.07%;
    margin: 0 auto;
    padding: 32px 100px 33px 79px;
    background-color: white;

    @include r(1239px) {
        padding: 30px 30px 40px;
    }

    @include r(767px) {
        grid-template-columns: 1fr;
        justify-content: center;
    }
}

.info__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    .info__text {
        margin-bottom: 30px;
        text-align: left;

        @include r(767px) {
            text-align: center;
        }
    }

    @include r(767px) {
        display: grid;
        justify-content: center;
        padding-top: 0;
    }
}

.info__title {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 40px;

    @include r(1023px) {
        font-size: 24px;
        line-height: 34px;
    }

    @include r(767px) {
        font-size: 18px;
        line-height: 28px;
        text-align: center;
    }

    &.benefits__title {
        text-align-last: left;
    }
}

.info__preview {
    img {
        display: block;
        width: 100%;
        height: auto;
        box-shadow: -3.14951px 0px 12.1894px rgba(76, 108, 125, 0.13),
            6.29902px 6.29902px 12.1894px rgba(76, 108, 125, 0.17);
    }

    @include r(767px) {
        max-width: 264px;
        margin: 0 auto;
    }
}

.partners {
    position: relative;
    padding: 65px 0 120px;
    background-color: $colorbggrey;

    @include r(1023px) {
        padding-bottom: 65px;
    }

    @include r(575px) {
        padding-top: 0;
    }
}

.partners__bg {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
    width: 94.38%;
    height: 84.62%;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);

    @include r(575px) {
        width: 100%;
    }
}

.partners__wrapper {
    display: grid;
    grid-template-columns: 55.71% 34.65%;
    gap: 0 9.64%;
    padding-right: 12.5%;

    @include r(1023px) {
        grid-template-columns: 53% 35%;
        gap: 0 5%;
        padding-right: 4%;
    }

    @include r(1023px) {
        grid-template-columns: 48% 44%;
    }

    @include r(575px) {
        grid-template-columns: 1fr;
        padding-right: 0;
    }
}

.partners__image {
    picture {
        width: 100%;
    }

    img {
        display: block;
        width: 100%;
        height: auto;
    }

    @include r(575px) {
        margin-left: -16px;
        margin-right: -16px;
    }
}

.partners__content {
    padding-top: 216px;

    @include r(1239px) {
        padding-top: 140px;
    }

    @include r(767px) {
        padding-top: 80px;
    }

    @include r(575px) {
        padding-top: 40px;
    }
}

.partners__title {
    margin-bottom: 15px;
}

.partners__subtitle {
    margin-bottom: 15px;
    font-family: $fontmont;
    font-weight: 600;
    font-size: 24px;
    line-height: 34px;
    color: $colortxt;

    @include r(1023px) {
        font-size: 18px;
        line-height: 28px;
    }

    @include r(767px) {
        font-size: 16px;
        line-height: 24px;
    }
}

