.section-ask__title {
    @include r(575px) {
        padding-left: 16px;
        padding-right: 16px;
    }
}

// advantages styles

.advantages__content {
    position: relative;
    overflow: hidden;
}

.advantages__title {
    margin-bottom: 40px;
    text-align: center;
}

.advantages__bg {
    width: 183%;
    min-width: 1280px;
}

.advantages__wrapper {
    margin-bottom: 130px;
}

.advantages__item {
    max-width: 800px;
    display: grid;
    grid-template-columns: 132px 1fr;
    gap: 0 33px;
    align-items: center;
    margin: 0 auto 20px;
    padding: 49px 70px 49px 50px;
    background-color: white;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);

    &:nth-child(4) {
        margin-bottom: 40px;
    }

    @include r(575px) {
        padding: 30px;
        grid-template-columns: 1fr;
        gap: 10px 0;
    }
}

.advantages__step {
    margin-bottom: 0;
    font-family: $fontmont;
    font-weight: 700;
    font-size: 96px;
    line-height: 117px;
    text-align: center;
    color: $colorblue;

    @include r(767px) {
        font-size: 76px;
        line-height: 97px;
    }

    @include r(575px) {
        font-size: 55px;
        line-height: 77px;
    }
}

.advantages__descr {
    margin-bottom: 0;
    font-size: 20px;
    line-height: 30px;

    @include r(575px) {
        text-align: center;
    }
}

.advantages__button {
    display: flex;
    justify-content: center;
}

.advantages__link {
    cursor: pointer;
}

.invitation {
    position: relative;
    overflow: hidden;
    height: 800px;
    @include r(1920px) {
        height: 575px;
    }
    @include r(1399px) {
        height: 500px;
    }
    @include r(1023px) {
        height: 400px;
    }
    @include r(767px) {
        height: 300px;
    }
}

.invitation__bg {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
    max-width: 2560px;
    z-index: -1;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.invitation__wrapper {
    position: relative;
    max-width: 1440px;
    height: 100%;
    margin: 0 auto;
    z-index: 5;

    @include r(767px) {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 16px;
    }
}

.invitation__content {
    position: absolute;
    top: 167px;
    left: 81px;
    z-index: 5;
    width: 590px;
    height: 257px;
    padding: 70px;
    background-color: white;

    @include r(1239px) {
        top: 157px;
        left: 50px;
        width: 550px;
        height: 240px;
        padding: 50px;
    }

    @include r(1023px) {
        top: 107px;
        left: 30px;
        width: 400px;
        height: 190px;
        padding: 40px;
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);
    }

    @include r(767px) {
        position: static;
        margin: auto;
    }

    // @include r(575px) {
    //     width: 100%;
    //     margin: auto 0;
    //     box-shadow: none;
    // }
}

.invitation__title {
    margin-bottom: 15px;
    font-family: $fontmont;
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;

    @include r(1023px) {
        font-size: 24px;
        line-height: 34px;
    }

    @include r(767px) {
        font-size: 24px;
        line-height: 34px;
    }
}

.rpo_advantages {
    position: relative;

    @include r(767px) {
        margin-bottom: 0;
        padding-bottom: 40px;
        background-color: $colorbggrey;
    }

    @include r(575px) {
        background-color: white;
    }
}

.rpo_advantages__wrapper {
    position: relative;
    display: grid;
    grid-template-columns: 43.95% 42.77%;
    gap: 0 13.28%;
    padding-right: 3.13%;
    padding-bottom: 91px;

    @include r(1023px) {
        padding-right: 0;
        grid-template-columns: 40% 52%;
        gap: 0 8%;
        padding-bottom: 10px;
    }

    @include r(575px) {
        grid-template-columns: 1fr;
        gap: 0;
    }
}

.rpo_advantages__bg {
    position: absolute;
    top: 0;
    left: -80px;
    width: 40%;
    height: 100%;
    z-index: -1;
    background-color: $colorbggrey;

    @include r(1399px) {
        left: -30px;
    }

    @include r(767px) {
        display: none;
    }
}

.rpo_advantages__images {
    padding-top: 69px;

    img {
        display: block;
        width: 100%;
        height: auto;
    }

    @include r(767px) {
        padding-top: 0;
        width: calc(100% + 32px);
        margin-left: -16px;
        max-height: 500px;
        object-fit: cover;
    }
}

.rpo_advantages__content {
    padding-top: 95px;

    @include r(1023px) {
        padding-top: 50px;
    }
}

.rpo_advantages__title {
    margin-bottom: 40px;
    text-align: left;

    @include r(1023px) {
        margin-bottom: 30px;
        // font-size: 20px;
        // line-height: 30px;
    }

    @include r(767px) {
        margin-bottom: 20px;
        // font-size: 16px;
        // line-height: 24px;
    }
}

.rpo_advantages__list {
    list-style-type: none;
    padding-left: 30px;

    @include r(767px) {
        padding-left: 50px;
    }
}

.rpo_advantages__item {
    position: relative;
    margin-bottom: 20px;
    font-size: 20px;
    line-height: 30px;

    &::before {
        content: "";
        position: absolute;
        top: 9px;
        left: -25px;
        width: 9px;
        height: 9px;
        background-color: $colorblue;
        transform: rotate(-45deg);
    }

    @include r(1023px) {
        font-size: 18px;
        line-height: 28px;
    }

    @include r(767px) {
        font-size: 16px;
        line-height: 24px;
        left: -20px;
    }
}

.rpo_invitation__wrapper {
    position: relative;
    max-width: 1440px;
    margin: 0 auto;
    height: 500px;

    display: grid;
    grid-template-columns: 44.8% 55.2%;

    @include r(767px) {
        height: auto;
        grid-template-columns: 1fr;
        background-color: $colorbluelight;
    }
}

.rpo__invitation__bg {
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    background-color: $colorbluelight;
}

.rpo_invitation__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 60px 15.5% 10px 12.4%;

    @include r(1023px) {
        padding: 60px 15.5% 10px 12.4%;
    }

    @include r(767px) {
        min-height: 300px;
        padding: 50px 10% 10px 10%;
    }

    @include r(575px) {
        padding: 30px 16px 15px;
    }
}

.rpo_invitation__title {
    width: 100%;
    margin-bottom: 20px;

    @include r(1023px) {
        margin-bottom: 10px;
    }
}

.rpo_invitation__subtitle.career__main_text {
    margin-left: 0;
    text-align: left;
}

.rpo_invitation__image {
    height: 500px;

    @include r(767px) {
        height: auto;
        max-height: 350px;
    }

    @include r(575px) {
        max-height: 300px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
