.section-resume__container
    height: 100%
    display: flex
    justify-content: space-between
    align-items: center
    max-width: 1275px
    @include r(767px)
        flex-direction: column
        align-items: center
        justify-content: center
        text-align: center

.section-resume__left
    margin-right: 100px
    @include r(767px)
        margin-right: 0
        margin-bottom: 30px
        max-width: 400px

.section-resume__right
    flex: none

.section-resume__title
    font-weight: 600
    margin-bottom: 10px

.section-resume__descr
    margin-bottom: 0