.more
    line-height: 19px
    color: $colorblue
    display: inline-block
    position: relative
    padding-right: 40px
    padding-bottom: 8px
    &::before
        content: ''
        display: block
        width: 19px
        height: 19px
        background: url(../images/plus.svg) no-repeat center / 19px auto
        position: absolute
        right: 0
        top: 0
    &::after
        content: ''
        display: block
        width: 0
        height: 2px
        background: $colorblue
        position: absolute
        bottom: 0
        left: 0
        margin-top: 8px
        transition: 0.2s
    &:hover
        &::after
            width: 100%
    @include r(1023px)
        font-size: 18px
        line-height: 19px
    @include r(767px)
        font-size: 16px
        line-height: 16px
        padding-right: 23px
        &::before
            width: 12px
            height: 12px
            background-size: 12px auto
            top: 3px
