// header hover effect
.navmain__link {
    & span {
        // position: absolute;
        // top: 50%;
        // right: -25px;
        padding: 0 6px;
        // transform: translateY(-50%);
    }

    & path,
    & svg {
        transition: all 0.2s linear;
    }

    @include r(1023px) {
        span {
            top: 8px;
            right: -45px;
        }
        svg {
            display: inline-block;
            width: 20px;
            height: auto;
        }
        &:hover path {
            fill: $colorblue;
        }
    }
}

.navmain__item {
    @include r(1023px) {
        margin: 0;

        &:not(:last-child) {
            margin-bottom: 15px;
        }

        &:not(:first-child) {
            margin-top: 15px;
        }
    }
}

// .navmain__item:nth-child(5),
// .navmain__item:last-child {
//     position: relative;
//     left: 5px;

//     @include r(1023px) {
//         position: static;
//     }
// }

#without__dropdown {
    @include r(1023px) {
        margin-bottom: 30px;
    }
}

.navmain__link.active {
    & svg {
        transform: scaleY(-1);
    }

    & path {
        fill: $colorblue;
    }
}

.navmain__item.open {
    & svg {
        transform: scaleY(-1);
    }
}

.navmain__item.active {
    &::after {
        display: block;
    }
}

// mobile menu update

.nav__subitem {
    display: none;
    overflow: hidden;

    @include r(1023px) {
        display: block;
        max-height: 0;
        transition: all 0.2s ease;
    }
}

.nav__sublink {
    display: block;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 10px;
    text-decoration: none;

    @include r(767px) {
        font-size: 16px;
        line-height: 24px;
    }
}

// dropdown styles

.header__dropdown {
    position: absolute;
    left: 0;
    top: 90px;
    width: 100%;
    background-color: white;
    box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.06);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s linear;
    border-top: 1px solid $colorborder;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}

.dropdown {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;

    display: flex;
    justify-content: center;

    @include r(1023px) {
        display: none;
    }
}

#talentSolutions .dropdown__item {
    width: 33.33%;
}

#getToKnow .dropdown__item {
    width: 100%;
    max-width: 720px;
    text-align: center;
}

#opportunities .dropdown__item {
    width: 50%;
}

.dropdown__item {
    position: relative;
    max-width: 720px;
    padding: 50px 8px 50px 27px;
    background-color: #fff;

    &:not(:last-child) {
        border-right: 1px solid $colorborder;
    }
}

.dropdown__item:hover {
    background-color: $colorbluelight;

    & .dropdown__title {
        color: $colorblue;
    }
}

h3.dropdown__title {
    margin: 0;
    font-family: $fontdefault;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: $colortxt;
}

p.dropdown__text {
    margin: 0;
    font-family: $fontdefault;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: $colortextgrey;
}

.dropdown__link {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 5;
}
