.ask
    margin: 0
    padding: 0
    list-style: none
    display: flex
    align-items: flex-start
    justify-content: space-between
    @include r(767px)
        flex-direction: column
        align-items: center

.ask__item
    max-width: 255px
    width: 30%
    flex: none
    text-align: center
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.7s
    .is-animated &
        opacity: 1
        transform: none
    &:nth-child(2)
        transition: opacity 1.5s $transitiondefault, transform 1.3s $transitiondefault
        transition-delay: 1s
    &:nth-child(3)
        transition: opacity 1.5s $transitiondefault, transform 1.5s $transitiondefault
        transition-delay: 1.3s
    @include r(767px)
        width: 100%
        max-width: 200px
        margin-bottom: 50px
        &:last-child
            margin-bottom: 0

.ask__icon
    margin: 0 auto 30px
    @include r(767px)
        width: 70px
        margin-bottom: 20px

.ask__txt
    font-weight: bold
    color: $colorblue
    margin-bottom: 0


