.section
    margin-bottom: 115px
    @include r(1023px)
        margin-bottom: 100px
    @include r(767px)
        margin-bottom: 80px

.section_mb_l
    margin-bottom: 150px
    @include r(1023px)
        margin-bottom: 100px
    @include r(767px)
        margin-bottom: 80px

.section-bluelight
    padding: 90px 0
    background-color: $colorbluelight
    @include r(1023px)
        padding: 60px 0
    @include r(767px)
        padding: 50px 0

.section-bluelight_narrow
    padding: 45px 0

.section-bluelight_figures
    background-image: url(../images/decor-figures.svg) 
    background-repeat: no-repeat 
    background-position: right 10px top 10px
    background-size: 490px auto
    @include r(767px)
        background-position: right 30% bottom 10px
        background-size: 300px auto

.section-greylight
    padding: 90px 0
    background-color: $colorbggrey
    @include r(1023px)
        padding: 60px 0
    @include r(767px)
        padding: 50px 0

.section-greylight_circles
    position: relative
    overflow: hidden
    &::before,
    &::after
        content: ''
        display: block
        background: rgba(255, 255, 255, 0.56)
        border-radius: 50%
        position: absolute
    &::before
        width: 210px
        height: 210px
        top: -3px
        left: -70px
    &::after
        width: 603px
        height: 603px
        right: -260px
        bottom: -185px

.section-greylight__container
    position: relative
    z-index: 1