.footer
    flex: none
    padding-top: 25px
    border-top: 1px solid $colorborder

.footer__top
    display: flex
    align-items: flex-end
    margin-bottom: 35px
    @include r(1023px)
        margin-bottom: 25px
    @include r(767px)
        display: block
        margin-bottom: 30px

.footer__logo
    flex: none
    margin-top: 5px
    display: block
    @include r(1023px)
        margin-top: 0
    @include r(767px)
        margin-bottom: 14px

.footer__logo-img
    display: block
    @include r(1023px)
        width: 153px
        height: 30px

.footer__descr
    flex-grow: 1
    margin-left: 48px
    margin-bottom: 0
    font-size: 14px
    line-height: 20px
    color: $colortxtgreylight
    max-width: 1000px
    @include r(1023px)
        margin-left: 25px
        margin-bottom: 0
    @include r(767px)
        margin-left: 0
        font-size: 12px
        line-height: 18px

.footer__center
    display: flex
    justify-content: space-between
    align-items: flex-start
    margin-bottom: 33px
    padding-right: 20px
    margin-left: -75px
    @include r(1399px)
        padding-right: 0
        margin-left: -40px
    @include r(1199px)
        flex-wrap: wrap
        margin-left: -10px
        margin-bottom: 40px
    @include r(767px)
        margin-bottom: 40px

.footer__navfooter
    max-width: 165px
    margin-left: 75px
    @include r(1399px)
        margin-left: 40px
    @include r(1199px)
        margin-left: 10px
    @include r(767px)
        padding-left: 10px
        margin-left: 0
        width: 50%
        margin-bottom: 25px

.footer__navfooter_last
    @include r(1199px)
        width: 50%
        flex: none
        margin-top: 45px
    @include r(767px)
        margin-top: 0
        margin-bottom: 0

.footer__sc
    margin-top: 30px
    @include r(767px)
        margin-top: 20px

.footer__subscribe
    flex: none
    margin-left: auto
    margin-right: -20px
    @include r(1399px)
        margin-right: 0
    @include r(1199px)
        width: 100%
        margin-left: 0
        margin-top: 50px
        padding-left: 10px
        flex: auto
    @include r(767px)
        margin-top: 10px

.footer__panel
    border-top: 2px solid $colorbggrey
    padding: 25px 0
    @include r(767px)
        padding: 20px 0

.footer__container
    display: flex
    align-items: center
    justify-content: space-between
    @include r(1023px)
        align-items: flex-start
    @include r(767px)
        flex-direction: column
        align-items: center

.footer__info
    display: flex
    align-items: center
    @include r(1023px)
        display: block
    @include r(767px)
        text-align: center

.footer__copyright
    flex: none
    margin-bottom: 0
    font-size: 14px
    line-height: 20px
    color: $colortxtgreylight
    @include r(767px)
        font-size: 12px
        line-height: 18px

.footer__dev
    flex: none
    font-size: 14px
    line-height: 20px
    color: $colortxtgreylight
    position: relative
    padding-left: 15px
    margin-left: 15px
    margin-bottom: 0
    &::before
        content: ''
        display: block
        width: 1px
        height: 15px
        background: $colortxtgreylight
        position: absolute
        left: 0
        top: 50%
        transform: translateY(-50%)
    a
        color: $colortxtgreylight
        &:hover
            color: $colortxt
    @include r(1023px)
        margin-left: 0
        padding-left: 0
        &::before
            content: none
    @include r(767px)
        font-size: 12px
        line-height: 18px

.footer__navbottom
    @include r(767px)
        order: -1
