.subscribe
    @include r(1199px)
        text-align: center

.subscribe__title
    font-weight: bold
    margin-bottom: 15px
    font-size: 18px
    line-height: 28px

.subscribe__form
    display: flex
    width: 380px
    @include r(1199px)
        margin: 0 auto
        width: 100%
        max-width: 515px
    @include r(767px)
        flex-direction: column
        max-width: 400px

.subscribe__input[type="email"]
    flex-grow: 1
    height: 45px
    border-radius: 3px 0 0 3px
    padding-top: 8px
    padding-bottom: 12px
    border-right: 0
    @include r(767px)
        margin-bottom: 20px
        border-radius: 3px
        border-right: 1px solid $colorborder

.subscribe__btn
    flex: none
    box-shadow: none
    border-radius: 0 3px 3px 0
    @include r(767px)
        border-radius: 3px