.slider-main
    position: relative
    .slick-list,
    .slick-track
        height: 100%
    .slick-prev,
    .slick-next
        position: absolute
        bottom: 40px
        &::before
            background: url(../images/arrow-white.svg) no-repeat center
        &:hover
            &::before
                background: url(../images/arrow-black.svg) no-repeat center
                background-color: white
        @include r(1023px)
            bottom: 50px
    .slider-main__descr
        margin-bottom: 96px
        @include r(1023px)
            margin-bottom: 0
    .slick-prev
        left: 70px
        @include r(1199px)
            left: 30px
        @include r(1023px)
            left: auto
            right: 134px
    .slick-next
        left: 140px
        &::before
            border-color: white
        @include r(1199px)
            left: 100px
        @include r(1023px)
            left: auto
            right: 64px
    .slick-dots
        left: 50%
        transform: translateX(-50%)
        bottom: 22px
        li
            width: 10px
            height: 10px
            margin: 0 2px
            button
                background: white
                width: 8px
                height: 8px
            &.slick-active
                button
                    background: white
                    width: 10px
                    height: 10px

.slider-main__slide
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    background-color: #5E6D7C
    height: 100%
    padding: 80px
    position: relative
    &::before
        content: ''
        display: block
        width: 100%
        height: 100%
        background: rgba(black, 0.4)
        position: absolute
        top: 0
        left: 0
        z-index: 1
    &::after
        content: ''
        display: block
        width: 0
        height: 4px
        background: $colorblue
        z-index: 5
        position: absolute
        bottom: 0
        left: 0
    @include r(1199px)
        padding-left: 30px
        padding-right: 30px
    @include r(1023px)
        padding-left: 54px
        padding-right: 54px
        padding-bottom: 32px
    @include r(767px)
        padding: 33px 16px
        &::after
            height: 3px

    &:nth-child(2)
        .slider-main__img
            transform-origin: bottom right

.is-autoplayed .slider-main__slide.slick-current
    &::after
        animation: 11.5s linear elementWidth
        animation-fill-mode: forwards

    .slider-main__img
        transform: scale(1.1)

.slider-main__img-box
    overflow: hidden
    width: 100%
    height: 100%
    position: absolute
    top: 0
    left: 0

.slider-main__img
    width: 100%
    height: 100%
    display: block
    object-fit: cover
    transform: scale(1)
    transform-origin: bottom left
    transition: transform 11.5s linear

.slider-main__content
    display: flex
    flex-direction: column
    justify-content: flex-end
    height: 100%
    position: relative
    color: white
    z-index: 2

.slider-main__title
    font: bold 36px/46px $fontmont
    margin-bottom: 20px
    @include r(1023px)
        font-size: 30px
        line-height: 40px
    @include r(767px)
        font-size: 24px
        line-height: 34px
        margin-bottom: 10px

.slider-main__descr
    margin-bottom: 0
    @include r(1023px)
        max-width: 375px
