.section-career
    overflow: hidden

.section-career__intro
    text-align: center
    width: 100%
    max-width: 625px
    margin: 0 auto 85px
    @include r(1023px)
        margin-bottom: 50px
    @include r(767px)
        margin-bottom: 30px

.section-career__title
    margin-bottom: 20px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        margin-bottom: 15px
    @include r(575px)
        margin-bottom: 10px

.section-career__subtitle
    opacity: 0
    transform: translate(0, 30%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.5s $transitiondefault
    transition-delay: 0.3s
    .is-animated &
        opacity: 1
        transform: none

.section-career__jobpanel
    opacity: 0
    transform: translateX(-100vw)
    transition: opacity 1.5s $transitiondefault, transform 1.5s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
        transform: none
