.slider-reviews
    background: white url(../images/slider-reviews-bg.svg) no-repeat right top / 100% auto
    border-radius: 3px
    box-shadow: 0px 4px 50px rgba(0, 164, 244, 0.16)
    padding: 60px 435px 120px 190px
    .slick-prev,
    .slick-next
        bottom: 43px
        left: auto
        @include r(1023px)
            bottom: 25px
    .slick-prev
        left: auto
        right: 250px
        @include r(1023px)
            right: 97px
    .slick-next
        right: 180px
        @include r(1023px)
            right: 27px
    .slick-dots
        top: 50%
        transform: translateY(-50%)
        right: 112px
        display: block
        li
            margin: 12px 0
    @include r(1199px)
        padding: 60px 230px 130px 130px
    @include r(1023px)
        padding: 55px 230px 60px 60px
        .slick-dots
            right: 35px
    @include r(767px)
        padding: 30px 30px 50px
        .slick-dots
            right: auto
            left: 50%
            transform: translateX(-50%)
            bottom: 33px
            top: auto
            display: flex 
            li
                margin: 0 2px

.slider-reviews__title
    color: $colorblue
    font-size: 36px
    line-height: 44px
    font-weight: bold
    font-family: $fontmont
    margin-bottom: 14px
    opacity: 0
    transform: translate(10%, 0)
    transition: opacity 1.5s $transitiondefault, transform 1.3s $transitiondefault
    @include r(1023px)
        font-size: 30px
        line-height: 40px
    @include r(767px)
        font-size: 24px
        line-height: 34px

.slider-reviews__pos
    font-family: $fontmont
    color: $colortxtgreylight
    margin-bottom: 20px
    opacity: 0
    transform: translate(10%, 0)
    transition: opacity 1.5s $transitiondefault, transform 1.3s $transitiondefault
    transition-delay: 0.1s
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px

.slider-reviews__txt
    opacity: 0
    transform: translate(10%, 0)
    transition: opacity 1.5s $transitiondefault, transform 1.3s $transitiondefault
    transition-delay: 0.2s
    
.start-slide-effect .slider-reviews__slide.slick-current
    .slider-reviews__title,
    .slider-reviews__pos,
    .slider-reviews__txt
        opacity: 1
        transform: none



