.for-input
    display: block
    position: relative
    &.is-error
        .for-input__input
            border-color: $colorerror
        .for-input__error
            display: block

.for-input__name
    font-size: 16px
    line-height: 22px
    display: block
    margin-bottom: 8px

.for-input__error
    font-size: 14px
    line-height: 22px
    color: $colorerror
    display: none

.for-input__upload
    display: block
    width: 100%
    background: white
    border-radius: 3px
    border: 1px solid $colorborder
    height: 55px
    font: normal normal 16px/23px $fontdefault
    color: $colortxtgrey
    padding: 15px 50px 15px 15px
    position: relative

.for-input__upload-icon
    position: absolute
    right: 20px
    top: 50%
    transform: translateY(-50%)

.for-input__upload-input
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    z-index: 1
    font-size: 0
    line-height: 0
    opacity: 0
    cursor: pointer

.for-input__upload-name
    display: block
    white-space: nowrap
    overflow: hidden
    text-overflow: ellipsis
    color: $colorborder
    &.has-file
        color: $colortxt