.section-benefits
    display: flex
    // align-items: flex-end
    align-items: center
    min-height: 816px
    // padding-top: 80px
    position: relative
    @include r(1023px)
        // margin-left: -24px
        // margin-right: -24px
        overflow: hidden
    @include r(767px)
        min-height: 0
        padding-top: 0
        margin-left: 0
        margin-right: 0
    // @include r(545px)
    //     margin-left: -16px
    //     margin-right: -16px

.section-benefits__container
    position: relative
    z-index: 2
    max-width: 1060px
    width: 100%
    margin: 0 auto
    @include r(1023px)
        padding: 0 24px
    @include r(767px)
        padding: 0

.section-benefits__bg
    position: absolute
    top: -60px
    left: -60px
    z-index: -1
    width: 410px
    height: 511px
    background-color: $colorbggrey
    @include r(767px)
        display: none

.section-benefits__content
    max-width: 504px
    width: 100%
    background: white
    padding: 60px 60px 83px 60px
    position: relative
    z-index: 1
    box-shadow: 0px 2px 15px rgba(211, 211, 211, 0.65)
    // opacity: 0
    // transform: translateX(-100vw)
    // transition: opacity 1s $transitiondefault, transform 0.8s $transitiondefault
    // transition-delay: 2s
    .is-animated &
        opacity: 1
        transform: none
    @include r(767px)
        padding: 30px 16px
        margin: 0 auto
        // text-align: center
        // opacity: 1
        // transform: none

.section-benefits__title
    margin-bottom: 30px
    font-size: 24px
    line-height: 34px
    font-family: $fontdefault
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px

.section-benefits__list
    padding: 0
    font-size: 16px
    line-height: 24px
    margin-bottom: 40px
    @include r(767px)
        text-align: left
    li
        margin-left: 0

.section-benefits__wall
    position: absolute
    // right: -80px
    right: 0
    top: 0
    @include r(1399px)
        // right: -30px
    @include r(1023px)
        right: 0
    @include r(767px)
        display: none
