.section-solutions__container
    display: flex
    align-items: center
    justify-content: space-between
    overflow: hidden
    @include r(1023px)
        display: block

.section-solutions__left
    flex-grow: 1
    margin-right: 60px
    @include r(1023px)
        margin-right: 0
        margin-bottom: 70px
    @include r(767px)
        margin-bottom: 60px

.section-solutions__right
    width: 37.5%
    flex: none
    @include r(1023px)
        width: 100%
    @include r(767px)
        width: auto
        margin-left: -16px
        margin-right: -16px

.section-solutions__title
    margin-bottom: 60px
    opacity: 0
    transform: translate(0, 101%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        margin-bottom: 40px
