.slider-dots
    margin: 0
    padding: 0
    list-style: none
    &.is-hidden
        display: none
        visibility: hidden
        z-index: -1

.slider-dots__slide
    height: 27px !important
    @include r(767px)
        height: 10px !important
        position: relative
    .slick-vertical &
        border: 0
    &.slick-current
        .slider-dots__dot
            width: 14px
            height: 14px
            background: $colorblue
            @include r(767px)
                width: 10px
                height: 10px
    &.slick-current + .slick-active
        padding-top: 1px
        @include r(767px)
            padding-top: 0
    &.slick-current + .slick-active + .slick-active
        padding-top: 1px
        @include r(767px)
            padding-top: 0
        .slider-dots__dot
            width: 10px
            height: 10px
            @include r(767px)
                width: 6px
                height: 6px
    &.slick-current + .slick-active + .slick-active + .slick-active
        .slider-dots__dot
            width: 8px
            height: 8px
            @include r(767px)
                width: 4px 
                height: 4px
    &:focus
        outline: 0

.slider-dots__dot
    width: 12px
    height: 12px
    border-radius: 50%
    margin: 0 auto
    background: #CED4D8
    transition: 0.2s
    @include r(767px)
        width: 8px 
        height: 8px
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
