.slider-video
    padding-bottom: 85px
    padding-left: calc((100vw - 1280px)/2)
    @include r(1439px)
        padding-left: 80px
    @include r(1399px)
        padding-left: 30px
    @include r(1023px)
        padding-left: 24px
    @include r(767px)
        padding-left: 16px

    .slick-prev,
    .slick-next
        bottom: 0
        left: auto
        right: 50%
        @include r(1439px)
            right: 80px
        @include r(1399px)
            right: 30px
        @include r(1023px)
            right: 24px
        @include r(767px)
            right: 16px
    .slick-prev
        margin-right: -570px
        @include r(1439px)
            margin-right: 0
            right: 150px
        @include r(1399px)
            right: 100px
        @include r(1023px)
            right: 94px
        @include r(767px)
            right: 86px
    .slick-next
        margin-right: -640px
        @include r(1439px)
            margin-right: 0
        
.slider-video__link
    text-decoration: none
    display: block
    width: 406px
    color: $colortxt
    margin-right: 30px
    &:hover, &:focus
        outline: 0
        .slider-video__mock::after
            background: rgba(0, 0, 0, 0.5)

        .slider-video__title
            color: $colorblue
    @include r(1023px)
        width: 297px

.slider-video__mock
    position: relative
    height: 250px
    margin-bottom: 25px
    &::after
        content: ''
        display: block
        width: 100%
        height: 100%
        background: rgba(0, 0, 0, 0.4)
        position: absolute
        top: 0
        left: 0
        transition: 0.4s
    @include r(1023px)
        height: 215px

.slider-video__mock-img
    position: absolute
    top: 0
    left: 0
    display: block
    width: 100%
    height: 100%
    object-fit: cover

.slider-video__play
    position: absolute
    left: 50%
    top: 50%
    transform: translate(-50%, -50%)
    z-index: 1

.slider-video__title
    margin-bottom: 15px
    transition: 0.2s

.slider-video__descr
    margin-bottom: 0