.section-contact
    position: relative
    padding-top: 100px
    padding-bottom: 125px
    overflow: hidden
    @include r(1023px)
        padding-top: 60px
        padding-bottom: 70px
    @include r(767px)
        padding-top: 50px
        padding-bottom: 65px

.section-contact__container
    position: relative

.section-contact__heading
    margin-bottom: 60px
    @include r(767px)
        margin-bottom: 40px

.section-contact__title
    width: 100%
    max-width: 840px
    margin-left: auto
    margin-right: auto

.section-contact__form
    padding-bottom: 100px
    @include r(1023px)
        padding-bottom: 0

.section-contact__frame
    width: 100%
    max-width: 738px
    margin: 0 auto 30px
    @include r(370px)
        margin-left: -20px
        margin-right: -20px
        width: auto
    iframe
        width: 100%
        height: 100%
        border: 0
        display: block

.section-contact__frame_client
    height: 673px
    @include r(609px)
        height: 693px
    @include r(546px)
        height: 882px

.section-contact__frame_company
    height: 560px
    @include r(546px)
        height: 749px

.section-contact__btns
    display: flex
    flex-direction: column
    align-items: center

.section-contact__backlink
    color: $colorblue
    text-decoration: none
    padding: 0 30px
    position: relative
    display: block
    margin-top: 17px
    &::before
        content: ''
        display: block
        width: 14px
        height: 7px
        border: 1px solid transparent
        background: url(../images/arrow-blue.svg) no-repeat center / 14px auto
        transition: 0.2s
        transform: rotate(180deg)
        position: absolute
        left: 0
        top: 12px
    &:hover
        &::before
            left: -5px
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px
        &::before
            top: 10px
