.join-us__hero {
    margin-bottom: 60px;
}

.career {
    overflow: hidden;
    .container {
        position: relative;
        z-index: 2;
    }
}

.career__decor {
    position: relative;

    @include r(767px) {
        display: none;
    }
}

.career__circle_grey,
.career__circle_blue {
    position: absolute;
    z-index: -1;
    border-radius: 50%;
}

.career__circle_blue {
    top: 319px;
    left: -890px;
    background-color: $colorbluelight;
    width: 1296px;
    height: 1296px;

    @include r(1239px) {
        width: 1100px;
        height: 1100px;
    }

    @include r(1023px) {
        top: 260px;
    }
}

.career__circle_grey {
    top: 156px;
    right: -739px;
    background-color: $colorbggrey;
    width: 934px;
    height: 934px;

    @include r(1023px) {
        top: 110px;
    }
}

.career__main_text {
    max-width: 624px;
    margin: 0 auto 20px;
    font-family: $fontmont;
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    text-align: center;
    color: $colortxt;

    @include r(1023px) {
        font-size: 18px;
        line-height: 28px;
    }
}

.career__submit {
    margin-bottom: 39px;
    text-align: center;
}

.section-general__title {
    margin-bottom: 33px;
}

.values__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include r(575px) {
        grid-template-columns: repeat(1, 1fr);
    }
}

.values__item {
    min-height: 273px;
    display: grid;
    grid-template-columns: 105px 1fr;
    gap: 0 35px;
    padding: 50px 50px 30px 40px;
    background: white;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);

    @include r(1023px) {
        min-height: 240px;
        grid-template-columns: 80px 1fr;
        gap: 0 20px;
        padding: 30px;
    }

    @include r(767px) {
        min-height: 230px;
        grid-template-columns: 60px 1fr;
        gap: 0 15px;
        padding: 25px 20px;
    }
}

.values__title {
    margin-bottom: 5px;
}

.values__descr {
    margin-bottom: 0;
}

.values__icon {
    span {
        width: 100%;
    }
}

.benefits__wrapper {
    display: grid;
    grid-template-columns: 1fr 516px;
    gap: 140px;

    @include r(1239px) {
        gap: 80px;
    }

    @include r(1023px) {
        grid-template-columns: 1fr 410px;
        gap: 40px;
    }

    @include r(767px) {
        display: block;
    }
}

.benefits__image {
    img {
        display: block;
        width: 100%;
        height: auto;
    }

    @include r(1023px) {
        margin-left: -24px;
    }

    @include r(767px) {
        margin: 0 -16px 40px;

        img {
            max-height: 400px;
            object-fit: cover;
        }
    }

    @include r(575px) {
        img {
            max-height: 350px;
        }
    }
}

.benefits__title {
    margin-bottom: 20px;

    @include r(767px) {
        margin: 0 auto 20px 0;
        text-align: left;
    }
}

.benefits__list {
    list-style-type: none;

    @include r(767px) {
        max-width: 410px;
        margin: 0 auto 0 0;
        padding-left: 30px;
    }
}

.benefits__item {
    position: relative;
    font-size: 21px;
    line-height: 24px;

    &:not(:last-child) {
        margin-bottom: 20px;
    }

    &::before {
        content: "";
        position: absolute;
        top: 7px;
        left: -35px;
        width: 9px;
        height: 9px;
        background-color: $colorblue;
        transform: rotate(-45deg);

        @include r(767px) {
            left: -28px;
        }
    }

    @include r(1023px) {
        font-size: 16px;
        line-height: 20px;
    }
}
