.tabs__panel
    position: sticky
    top: 90px
    z-index: 500
    display: flex
    justify-content: center
    @include r(1023px)
        top: 70px

.tabs__link
    flex: none
    width: 50%
    height: 55px
    border-bottom: 2px solid $colorborder
    background: white
    font-size: 18px
    line-height: 53px
    font-weight: 500
    font-family: $fontmont
    text-decoration: none
    color: $colortxtgreylight
    display: flex
    transition: 0.2s
    &:first-child
        justify-content: flex-end
    &.current-tab
        color: $colorblue
        border-color: $colorblue
        background: $colorbluelight

    @include r(767px)
        font-size: 16px
        line-height: 38px
        height: 40px

.tabs__link-txt
    display: block
    width: 100%
    max-width: 770px
    text-align: center

.tabs__container
    overflow: hidden
    padding-top: 160px
    @include r(1023px)
        padding-top: 0

.tabs__content
    display: none
    &.current-tab
        display: block
