.features
    display: flex
    justify-content: space-between
    @include r(1023px)
        display: block

.features__left
    width: 50%
    flex: none
    max-width: 545px
    @include r(1399px)
        max-width: none
        width: auto
        flex: auto
        padding-right: 80px
    @include r(1199px)
        padding-right: 40px
    @include r(1023px)
        padding-right: 0

.features__right
    width: 50%
    flex: none
    @include r(1399px)
        margin-right: -30px
    @include r(1023px)
        display: none

.features__title
    margin-bottom: 20px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        text-align: center
        margin-bottom: 15px
    @include r(575px)
        margin-bottom: 10px

.features__descr
    margin-bottom: 40px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.3s $transitiondefault
    transition-delay: 0.2s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        text-align: center

.features__thumbs
    opacity: 0
    transform: translate(0, 10%)
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
        transform: none

.features__slider
    height: 100%
    opacity: 0
    transition: opacity 1.5s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
