.section-insights__container
    display: flex
    align-items: flex-start
    justify-content: flex-end
    @include r(1229px)
        justify-content: space-between
    @include r(767px)
        overflow-x: auto
        justify-content: flex-start
        align-items: stretch
        margin-bottom: 30px

.section-insights__left
    width: 545px
    flex: none
    @include r(1229px)
        width: 47%
    @include r(1023px)
        min-width: 345px
    @include r(767px)   
        width: auto
        min-width: 0
        margin-right: 16px
        margin-left: -16px

.section-insights__right
    width: 545px
    flex: none
    margin-left: 80px
    padding-top: 75px
    @include r(1229px)
        width: 47%
        margin-left: 0
    @include r(1023px)
        min-width: 345px
    @include r(767px)
        min-width: 0
        width: auto
        padding-top: 0

.section-insights__intro
    margin-bottom: 70px
    @include r(767px)
        display: none

.section-insights__intro_mob
    display: none
    @include r(767px)
        display: block
        padding: 0 16px
        text-align: center
        margin-bottom: 40px

.section-insights__heading
    margin-bottom: 30px

.section-insights__title
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none

.section-insights__subtitle
    font-weight: 500
    font-family: $fontmont
    color: $colortxtgrey
    opacity: 0
    transform: translate(0, 30%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.5s $transitiondefault
    transition-delay: 0.3s
    .is-animated &
        opacity: 1
        transform: none

.section-insights__btn-mob
    display: none
    @include r(767px)
        display: flex
        justify-content: center
        padding: 0 16px

.section-insights__btn 
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.7s $transitiondefault
    transition-delay: 0.5s
    .is-animated &
        opacity: 1
        transform: none
