.slider-press
    padding-bottom: 85px
    margin-left: -30px
    @include r(1399px)
        margin-right: -30px
    @include r(1023px)
        margin-right: -24px
    @include r(767px)
        margin-right: -16px
    .slick-prev,
    .slick-next
        bottom: 0
        left: auto
        right: 0
        @include r(1399px)
            right: 30px
        @include r(1023px)
            right: 24px
        @include r(767px)
            right: 16px
    .slick-prev
        right: 70px
        @include r(1399px)
            right: 100px
        @include r(1023px)
            right: 94px
        @include r(767px)
            right: 86px

.slider-press__link
    text-decoration: none
    display: block
    width: 297px
    color: $colortxt
    margin-left: 30px
    &:hover, &:focus
        outline: 0
        .slider-press__title
            color: $colorblue

        .slider-press__more::after
            width: 100%

.slider-press__img
    display: block
    width: 100%
    height: 215px
    object-fit: cover
    margin-bottom: 20px

.slider-press__title
    margin-bottom: 15px
    transition: 0.2s
