.blogpost
    text-decoration: none
    color: $colortxt
    position: relative
    display: block
    &:hover
        .blogpost__title
            color: $colorblue

        .blogpost__more::after
            width: 100%

.blogpost__img
    display: block
    width: 100%
    height: 670px
    object-fit: cover
    @include r(1023px)
        height: 400px
    @include r(767px)
        height: 330px

.blogpost__content
    background: white
    position: absolute
    left: 50px
    right: 50px
    bottom: 50px
    padding: 30px 40px
    z-index: 1
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.13)
    display: flex
    flex-direction: column
    @include r(1199px)
        left: 30px
        right: 30px
    @include r(1023px)
        bottom: 30px
    @include r(767px)
        left: 16px
        right: 16px
        bottom: 20px
        padding: 16px
    

.blogpost__title
    margin-bottom: 22px
    transition: 0.2s
    height: 56px
    overflow: hidden

.blogpost__bottom
    flex: none
    margin-top: auto
    display: flex
    align-items: flex-start
    justify-content: space-between
    margin-right: -40px
    @include r(767px)
        margin-right: -16px

.blogpost__more
    flex: none

.blogpost__time
    flex: none
    font-size: 16px
    line-height: 24px
    color: $colortxtgreylight
    padding-right: 40px
    white-space: nowrap
    position: relative
    margin-top: -3px
    &::before
        content: ''
        display: block
        height: 1px
        width: 35px
        position: absolute
        right: 0
        top: 50%
        transform: translateY(-50%)
        background: $colortxtgreylight
    @include r(767px)
        padding-right: 20px
        &::before
            width: 15px

.blogpost_preview
    display: flex
    height: 145px
    background: white
    box-shadow: 0 2px 10px rgba(0,0,0,.1)
    @include r(767px)
        height: auto
        flex-direction: column
        width: 297px

    .blogpost__img
        width: 188px
        height: 100%
        flex: none
        @include r(1199px)
            width: 140px
        @include r(1023px)
            width: 188px
        @include r(767px)
            width: 100%
            height: 150px
    
    .blogpost__content
        flex: auto
        position: relative
        left: 0
        right: 0
        bottom: 0
        padding: 20px 30px
        box-shadow: none
        @include r(1199px)
            padding: 20px
        @include r(1023px)
            padding: 25px 30px
        @include r(767px)
            padding: 16px

    .blogpost__title
        margin-bottom: 14px
        @include r(1199px)
            margin-bottom: 20px
        @include r(1023px)
            margin-bottom: 14px
        @include r(767px)
            margin-bottom: 22px

    .blogpost__bottom
        margin-right: -30px
        @include r(1199px)
            margin-right: -20px
        @include r(1023px)
            margin-right: -30px
        @include r(767px)
            margin-right: -16px

    .blogpost__time
        padding-right: 30px
        &::before
            width: 25px
        @include r(1199px)
            padding-right: 20px
            &::before
                width: 15px
        @include r(1023px)
            padding-right: 40px
            &::before
                width: 35px
        @include r(767px)
            padding-right: 20px
            &::before
                width: 15px
