.solutions-box
    padding: 40px
    height: 540px
    position: relative
    margin-right: -80px
    opacity: 0
    transform: translate(100%, 0)
    transition: opacity 1.5s $transitiondefault, transform 1.5s $transitiondefault
    transition-delay: 1.3s
    .is-animated &
        opacity: 1
        transform: none
    &::before
        content: ''
        display: block
        width: 66.66%
        height: 100%
        background: $colorbluelight
        position: absolute
        top: 0
        left: 0
    @include r(1399px)
        margin-right: -30px
        padding: 30px
    @include r(1199px)
        height: 400px
    @include r(1023px)
        padding: 0
        margin-right: 0
        height: auto
        &::before
            content: none

.solutions-box__img
    display: block
    width: 100%
    height: 100%
    object-fit: cover
    position: relative
    transform: translate(50%, 0)
    transition: transform 1.5s $transitiondefault
    transition-delay: 1.5s
    .is-animated &
        transform: none
    @include r(1023px)
        height: auto
        object-fit: contain
    