.navfooter
    margin: 0
    padding: 0
    list-style: none

.navfooter__item
    margin-bottom: 2px
    @include r(1023px)
        margin-bottom: 4px
    @include r(767px)
        margin-bottom: 5px

.navfooter__item_l
    margin-bottom: 6px
    @include r(1023px)
        margin-bottom: 10px
    @include r(767px)
        margin-bottom: 12px

.navfooter__link
    text-decoration: none
    font-weight: bold
    color: $colorblue
    &:hover
        color: $colortxt
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px

.navfooter__link_sm
    font-size: 16px
    line-height: 24px
    font-weight: normal
    color: $colortxt
    &:hover
        color: $colorblue
    @include r(767px)
        font-size: 14px
        line-height: 20px