.navmain
    @include r(1023px)
        position: fixed
        top: 0
        left: 0
        width: 100%
        height: 0
        background: white
        z-index: -1
        opacity: 0
        visibility: hidden
        transition: opacity .3s ease-in-out, visibility .3s ease-in-out
        overflow: hidden
        &.is-active
            height: 100%
            opacity: 1
            visibility: visible
            z-index: 5000
            overflow-y: auto
            .navmain__close
                display: block
            .navmain__item
                opacity: 1
                transform: none
            .navmain__sc
                opacity: 1
                transform: none

.navmain__content
    @include r(1023px)
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: space-between
        min-height: 100%
        padding: 130px 80px 100px 80px
    @include r(575px)
        padding: 110px 50px 100px 50px

.navmain__list
    flex-grow: 1
    display: flex
    align-items: center
    justify-content: center
    @include r(1023px)
        // flex-direction: column
        // align-items: flex-start
        // flex-grow: initial
        // margin: 135px 0 95px
        display: block
    @include r(767px)
        margin: 50px 0
    @include r(575px)
        margin: 0 0 50px 0

.navmain__item
    position: relative
    flex: none
    margin: 0 23px
    font-size: 16px
    line-height: 24px
    &:nth-child(1)
        width: 120px
    &:nth-child(3)
        width: 120px
    &:nth-child(5)
        width: 120px
    &:nth-child(6)
        width: 150px
    &:nth-child(8)
        width: 61px
    @include r(1199px)
        margin: 0 15px
        font-size: 14px
        line-height: 20px
    @include r(1023px)
        width: auto
        font: bold 30px/40px $fontmont
        margin: 45px 0
        flex: none
        transform: translateX(30%)
        opacity: 0
        transition: opacity .3s ease-in-out, transform .3s ease-in-out
        &:nth-child(3)
            transition-delay: .05s
        &:nth-child(5)
            transition-delay: .1s
        &:nth-child(6)
            transition-delay: .15s
        &:nth-child(8)
            transition-delay: .2s
    @include r(767px)
        font-size: 18px
        line-height: 28px
        margin: 20px 0

.navmain__link
    position: relative
    padding: 10px 0
    text-decoration: none
    white-space: nowrap
    &::before
        display: block
        content: attr(title)
        font-weight: bold
        height: 0
        overflow: hidden
        visibility: hidden
        @include r(1023px)
            display: none

    &:hover
        color: $colorblue
        font-weight: bold
    &.active
        color: $colorblue
        font-weight: bold

.navmain__item
    &::after
        display: none
        content: ""
        position: absolute
        top: 28px
        left: -20px
        width: 150%
        height: 40px
        cursor: pointer
        @include r(1023px)
            display: none

.navmain__link_current
    color: $colorblue
    font-weight: bold
    @include r(1023px)
        color: black

.navmain__close
    display: none
    position: fixed
    top: 30px
    right: 24px
    background: none
    padding: 0
    margin: 0
    border: 0
    box-shadow: none
    @include r(767px)
        top: 20px
        right: 16px

.navmain__sc
    display: none
    margin-bottom: 100px
    @include r(1023px)
        display: flex
        transform: translateX(30%)
        opacity: 0
        transition: opacity .3s ease-in-out, transform .3s ease-in-out
        transition-delay: .25s
        margin-bottom: 0
    // @include r(767px)
    //     margin-bottom: 50px
