.btn
    display: inline-flex
    justify-content: center
    align-items: center
    padding: 0 35px 3px
    font-weight: bold
    color: white
    background: $colorblue
    text-decoration: none
    height: 45px
    border-radius: 3px
    box-shadow: 0px 5px 6px rgba(0, 145, 216, 0.3)
    border: 2px solid $colorblue
    &:hover
        background: none
        color: $colorblue
        box-shadow: none
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px

.btn_l
    height: 70px
    padding-right: 43px
    padding-left: 43px
    @include r(1023px)
        height: 55px

.btn_transparent
    background: none
    color: $colorblue
    box-shadow: none
    &:hover
        background: $colorblue
        color: white

.btn_transparent-white
    background: none
    color: white
    border-color: white
    border-width: 1px
    box-shadow: none
    &:hover
        background: white
        color: black

.btn_icon-user
    &:hover
        @include svg-image-icon-user-hover

.btn_icon-download
    &:hover
        @include svg-image-icon-download-hover

.btn__icon-left
    margin-right: 20px
    flex: none

.btn__icon-right
    margin-left: 20px
    flex: none

