.form
    width: 100%
    max-width: 406px
    margin: 0 auto
    text-align: center

.form__fieldset
    border: 0
    padding: 0
    margin: 0 0 25px
    box-shadow: none
    display: block
    text-align: left

.form__for-input
    margin-bottom: 15px

.form__btns
    display: flex
    flex-direction: column
    align-items: center

.form__backlink
    color: $colorblue
    text-decoration: none
    padding: 0 30px
    position: relative
    display: block
    margin-top: 17px
    &::before
        content: ''
        display: block
        width: 14px
        height: 7px
        border: 1px solid transparent
        background: url(../images/arrow-blue.svg) no-repeat center / 14px auto
        transition: 0.2s
        transform: rotate(180deg)
        position: absolute
        left: 0
        top: 12px
    &:hover
        &::before
            left: -5px
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px
        &::before
            top: 10px



