.section-ask__title
    font-size: 24px
    line-height: 34px
    font-family: $fontmont
    font-weight: 500
    color: $colorblue
    text-align: center
    margin: 0 auto 44px
    width: 100%
    max-width: 915px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        margin-bottom: 30px
        max-width: 500px
        font-size: 16px
        line-height: 24px

.section-ask__descr
    font-size: 24px
    line-height: 34px
    font-family: $fontmont
    font-weight: 500
    color: $colorblue
    text-align: center
    margin: 0 auto 60px
    width: 100%
    max-width: 915px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1.5s $transitiondefault
    transition-delay: 0.4s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        margin-bottom: 40px
        max-width: 500px
        font-size: 16px
        line-height: 24px

.section-ask__ask
    width: 100%
    max-width: 1060px
    margin: 0 auto
