.slider
    .slick-slide:focus
        outline: 0

    &.slick-vertical .slick-slide
        border: 0

    .slick-prev,
    .slick-next
        position: absolute
        z-index: 1
        background: none
        padding: 0
        margin: 0
        border: 0
        font-size: 0
        line-height: 0
        color: transparent
        bottom: 0

        &::before
            content: ''
            display: block
            width: 60px
            height: 60px
            border: 1px solid transparent
            background: url(../images/arrow-blue.svg) no-repeat center
            transition: 0.2s
        
        &:hover
            &::before
                background: url(../images/arrow-white.svg) no-repeat center
                background-color: $colorblue

        &.slick-disabled
            opacity: 0.5
            pointer-events: none
    .slick-prev
        left: 0
        transform: rotate(180deg)
        &::before
            background: url(../images/arrow-grey.svg) no-repeat center
    .slick-next
        right: 0
        &::before
            border-color: $colorblue
    .slick-dots
        margin: 0
        padding: 0
        list-style: none
        position: absolute
        display: flex
        li
            width: 14px
            height: 14px
            position: relative
            margin: 0 12px
            &.slick-active button
                width: 14px
                height: 14px
                background: $colorblue
                pointer-events: none
            
            @include r(767px)
                width: 10px
                height: 10px
                margin: 0 2px
                &.slick-active button
                    width: 10px
                    height: 10px

        button
            display: block
            position: absolute
            left: 50%
            top: 50%
            transform: translate(-50%, -50%)
            width: 12px
            height: 12px
            padding: 0
            margin: 0
            border: 0
            box-shadow: none
            border-radius: 50%
            background: #CED4D8
            color: transparent
            font-size: 0
            line-height: 0
            transition: 0.2s
            &:hover,
            &:focus
                opacity: 0.7
                outline: 0

            @include r(767px)
                width: 8px
                height: 8px

