.header
    height: 90px
    flex: none
    background: white
    margin-bottom: 100px
    @include r(1023px)
        position: relative
        z-index: 950
        height: 70px
        margin-bottom: 55px
    @include r(767px)
        margin-bottom: 20px

.header_no-mb
    margin-bottom: 0
    @include r(1023px)
        margin-bottom: 55px
    @include r(767px)
        margin-bottom: 17px

.header__fixed
    background: white
    height: 90px
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06)
    position: fixed
    width: 100%
    left: 0
    top: 0
    z-index: 1000
    @include r(1023px)
        height: 70px

.header__container
    display: flex
    align-items: center
    height: 100%
    @include r(1199px)
        justify-content: space-between

.header__logo
    flex: none

.header__logo-img
    display: block
    @include r(1023px)
        width: 153px
        height: 30px

.header__hamburger
    display: none
    text-decoration: none
    background: none
    padding: 0
    margin: 0
    border: 0
    box-shadow: none
    @include r(1023px)
        display: block

.header__nav
    flex-grow: 1
    margin-left: 30px
    @include r(1199px)
        flex: none
    @include r(1023px)
        margin-left: 0
