.section-contact__container {
    position: relative;
}

.contacts__decor {
    @include r(767px) {
        display: none;
    }
}

.contacts__decor_rect_green {
    position: absolute;
    left: 222px;
    top: 83px;
    width: 47px;
    height: 47px;
    border: 4px solid #55d102;

    @include r(1023px) {
        left: 87px;
        top: 70px;
        width: 28px;
        height: 28px;
    }
}

.contacts__decor_dot {
    position: absolute;
    top: 130px;
    right: 302px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: $colorblue;

    @include r(1023px) {
        right: 127px;
        top: 70px;
        width: 9px;
        height: 9px;
    }
}

.contacts__decor_tandem {
    position: absolute;
    right: 197px;
    top: -30px;
    width: 101px;
    height: 86px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 86px;
        height: 86px;
        border: 4px solid white;
    }

    &::after {
        content: "";
        position: absolute;
        top: 27px;
        right: 0;
        width: 47px;
        height: 47px;
        border: 4px solid $colorblue;
    }

    @include r(1023px) {
        right: 72px;
        top: -10px;
        width: 61px;
        height: 52px;

        &::before {
            width: 52px;
            height: 52px;
        }

        &::after {
            top: 16px;
            width: 28px;
            height: 28px;
        }
    }
}

.contacts__decor_rect_white {
    position: absolute;
    top: 215px;
    right: 72px;
    width: 34px;
    height: 34px;
    border: 4px solid white;

    @include r(1023px) {
        right: 0;
        top: 140px;
        width: 20px;
        height: 20px;
    }
}

.contacts__decor_circle {
    position: absolute;
    top: 279px;
    left: -100px;
    z-index: 1;
    width: 167px;
    height: 167px;
    border-radius: 50%;
    background-color: white;

    @include r(1023px) {
        top: 323px;
        left: -50px;
        width: 100px;
        height: 100px;
    }
}

.contacts__decor_T {
    position: absolute;
    bottom: -73px;
    left: 1300px;
    width: 36px;
    height: 31px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 8px;
        background-color: #55d102;
    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 9px;
        height: 100%;
        background-color: #55d102;
    }
}

.contacts__decor_rectangles {
    position: absolute;
    top: 83px;
    left: 91px;
    width: 112px;
    height: 112px;
    z-index: -1;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 65px;
        height: 65px;
        background-color: $colorblue;
    }

    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 35px;
        height: 35px;
        background-color: $colorgreen;
    }

    @include r(1239px) {
        left: 40px;
    }

    @include r(1023px) {
        top: 10px;
        left: 30px;
    }
}

.contacts__decor_curves {
    position: absolute;
    top: 562px;
    left: 132px;
    width: 22px;
    height: 18px;
    background-color: white;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 4px solid $colorblue;
        border-radius: 0 0 4px 4px;
    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 18px;
        height: 6px;
        background-color: white;
    }

    @include r(1239px) {
        top: 540px;
        left: 110px;
    }
}

.contacts__decor_line_blue {
    position: absolute;
    top: 226px;
    right: 214px;
    width: 30px;
    height: 2px;
    transform: rotate(-110deg);
    background-color: $colorblue;

    @include r(1239px) {
        right: 130px;
    }
}

.contacts__decor_line_green {
    position: absolute;
    top: 344px;
    right: 128px;
    width: 30px;
    height: 2px;
    transform: rotate(-45deg);
    background-color: $colorgreen;

    @include r(1239px) {
        right: 90px;
    }
}

.contacts__decor_T_small {
    position: absolute;
    top: 504px;
    right: 198px;
    width: 22px;
    height: 18px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 4px;
        background-color: $colorgreen;
    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 6px;
        height: 100%;
        background-color: $colorgreen;
    }

    @include r(1239px) {
        right: 170px;
    }
}
