.section-top
    display: flex
    height: calc(100vh - 90px)
    min-height: 600px
    max-height: 850px
    @include r(1199px)
        max-height: 700px
    @include r(1023px)
        display: block
        height: auto
        min-height: 0
        max-height: none

.section-top__container
    height: 100%
    display: flex
    @include r(1023px)
        display: block

.section-top__left
    width: 50%
    flex: none
    display: flex
    justify-content: flex-end
    @include r(1023px)
        width: auto
        display: block
        text-align: center

.section-top__container-half
    display: flex
    flex-direction: column
    justify-content: center
    @include r(1023px)
        max-width: none
        display: block

.section-top__right
    width: 50%
    flex: none
    .section-top__container-half
        display: block
        height: 100%
        max-width: 960px
        padding: 0
    @include r(1023px)
        width: auto
        .section-top__container-half
            max-width: none

.section-top__slider
    height: 100%
    opacity: 0
    transition: opacity 1.5s $transitiondefault
    transition-delay: 1.2s
    .is-animated &
        opacity: 1
    @include r(1023px)
        height: 535px
    @include r(767px)
        height: 306px

.section-top__title
    flex: none
    font: 600 72px/88px $fontmont
    color: $colorblue
    position: relative
    padding-bottom: 35px
    margin: 100px 0 65px
    &::before
        content: ''
        display: block
        width: 0
        height: 9px
        background: $colorblue
        position: absolute
        bottom: 0
        left: 0
        transition: width 0.6s $transitiondefault
        transition-delay: 1s
        .is-animated &
            width: 79px
    @include r(1199px)
        font-size: 65px
        line-height: 75px
    @include r(1023px)
        margin-top: 0
        margin-bottom: 33px
        font-size: 54px
        line-height: 66px
        padding-bottom: 37px
        br
            display: none
        &::before
            left: 50%
            transform: translateX(-50%)
    @include r(767px)
        font-size: 30px
        line-height: 37px
        padding-bottom: 20px
        margin-bottom: 25px
        br
            display: block
        &::before
            height: 4px
            .is-animated &
                width: 40px

.section-top__title-word
    display: inline-block
    opacity: 0
    transform: translate(0, 101%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    .is-animated &
        opacity: 1
        transform: none
    &:first-child
        transition-delay: 0.1s
    &:nth-of-type(2)
        transition-delay: 0.2s
    &:nth-of-type(3)
        transition-delay: 0.4s
    &:nth-of-type(4)
        transition-delay: 0.5s
    &:nth-of-type(5)
        transition-delay: 0.7s

.section-top__descr
    flex: none
    width: 100%
    max-width: 545px
    margin-bottom: 130px
    opacity: 0
    transform: translate(0, 20%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1.2s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        margin-left: auto
        margin-right: auto
        margin-bottom: 50px
        max-width: 500px
    @include r(767px)
        margin-bottom: 30px