.solutions-list
    margin: 0
    padding: 0
    list-style: none
    display: flex
    justify-content: space-between
    @include r(767px)
        display: block

.solutions-list__item
    width: 45%
    flex: none
    opacity: 0
    transform: translate(0, 30%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 0.5s
    &:nth-child(2)
        transition-delay: 0.8s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        width: 48%
    @include r(767px)
        width: auto
        margin-bottom: 48px

.solutions-list__descr
    margin-bottom: 40px
    @include r(767px)
        margin-bottom: 30px