.article
    margin-bottom: 80px
    p, ol, ul
        margin-bottom: 40px
        @include r(767px)
            margin-bottom: 30px
        ul, ol
            margin-bottom: 20px

    .sm-mb
        margin-bottom: 10px

.article__container
    display: flex
    justify-content: center
    align-items: flex-start
    @include r(1239px)
        display: block
        max-width: 784px

.article__content
    width: 100%
    max-width: 724px
    flex: none
    padding: 0 50px
    margin-right: 200px
    @include r(1239px)
        margin-right: 0
        padding: 0
        max-width: none

.article__side
    position: sticky
    top: 120px
    flex: none
    width: 200px
    margin-top: 7px
    display: flex
    justify-content: flex-end
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1239px)
        position: static
        width: auto
        justify-content: flex-start
        margin-bottom: 15px

.article__title
    position: relative
    padding-bottom: 47px
    margin-bottom: 50px
    opacity: 0
    transform: translateX(-100vw)
    transition: opacity 1.5s $transitiondefault, transform 1.2s $transitiondefault
    .is-animated &
        opacity: 1
        transform: none
    &::before
        content: ''
        display: block
        width: 80px
        height: 9px
        background: $colorgreen
        position: absolute
        bottom: 0
        left: 0
        transform: translateX(-100vw)
        transition: transform 1.7s $transitiondefault
        .is-animated &
            transform: none

    @include r(767px)
        padding-bottom: 20px
        margin-bottom: 25px
        &::before
            width: 40px
            height: 4px



