.slider-features
    .slick-list,
    .slick-track
        height: 100%

.slider-features__slide
    height: 100%
    position: relative
    &:nth-child(odd)
        .slider-features__img
            transform-origin: bottom left
    &:nth-child(even)
        .slider-features__img
            transform-origin: bottom right

.slider-features__img
    width: 100%
    height: 100%
    display: block
    object-fit: cover
    position: absolute
    top: 0
    left: 0
    
    transition: transform 9s linear

.is-animated .slider-features__slide.slick-current
    .slider-features__img
        transform: scale(1.1)