.contacts__wrapper {
    position: relative;
    z-index: 2;
    max-width: 842px;
    margin: 0 auto;
    display: flex;
    gap: 30px;

    @include r(575px) {
        display: block;
    }
}

.contacts__item {
    position: relative;
    flex-grow: 1;
    padding: 33px 0 36px 12px;
    background-color: $colorblue;

    @include r(575px) {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:first-child {
            margin-bottom: 20px;
            padding: 18px 0 18px 17px;

            & .contacts__icon {
                width: 60px;
                height: 60px;
                margin-bottom: 0;
            }
        }

        &:last-child {
            padding: 11px 0 11px 8px;

            & .contacts__icon {
                width: 78px;
                height: 78px;
                margin-bottom: 0;
            }
        }
    }
}

.contacts__icon {
    width: 90px;
    height: 90px;
    margin-bottom: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contacts__text {
    max-width: 200px;
    margin-left: 16px;
    font-family: $fontdefault;
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: white;

    @include r(575px) {
        max-width: 150px;
        font-size: 16px;
        line-height: 20px;
    }

    span {
        text-transform: capitalize;
    }
}

.contacts__btn {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 65px;
    height: 65px;
    background-color: white;
    transition: all 0.2s linear;
    overflow: hidden;

    path {
        transition: all 0.2s linear;
    }

    @include r(575px) {
        position: relative;
    }
}

.contacts__arrow_first {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease-in-out;
}

.contacts__arrow_second {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-300%, -50%);
    transition: all 0.3s ease-in-out;
}

.contacts__item:hover {
    .contacts__arrow_first {
        transform: translate(300%, -50%);
    }

    .contacts__arrow_second {
        transform: translate(-50%, -50%);
    }
}

.contacts__link {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
}

.contacts_block__title {
    margin-bottom: 15px;

    @include r(1023px) {
        margin-bottom: 5px;
    }

    @include r(575px) {
        margin-bottom: 10px;
    }
}

.contacts_block__subtitle {
    font-family: $fontmont;
}
