.library
    text-align: center
    display: flex
    justify-content: space-around
    margin: 0
    padding: 0
    list-style: none
    @include r(767px)
        flex-direction: column
        align-items: center

.library__item
    width: 297px
    flex: none
    display: flex
    flex-direction: column
    position: relative
    &:hover
        .library__panel
            opacity: 1
    @include r(1199px)
        width: 250px
    @include r(1023px)
        width: 200px
    @include r(767px)
        margin-bottom: 40px
        width: 250px
        &:last-child
            margin-bottom: 0

.library__title
    font-family: $fontdefault
    margin-bottom: 30px
    @include r(767px)
        margin-bottom: 20px

.library__img
    margin-top: auto
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.12)
    width: 100%
    height: auto

.library__panel
    position: absolute
    bottom: 0
    left: 0
    right: 0
    background: rgba(0, 0, 0, 0.6)
    padding: 30px
    opacity: 0
    transition: 0.4s
    @include r(1023px)
        padding: 20px
    @media (hover: none)
        opacity: 1

.library__btn
    @include r(1023px)
        width: 100%
        padding: 0 15px