.contact
    margin: 0 0 50px
    padding: 0
    list-style: none
    display: flex
    justify-content: center
    @include r(767px)
        flex-direction: column
        justify-content: flex-start
        align-items: center

.contact__item
    width: 50%
    max-width: 436px
    padding: 0 15px
    flex: none
    @include r(1023px)
        max-width: 355px
    @include r(767px)
        padding: 0
        margin-bottom: 30px
        width: 100%
        &:last-child
            margin-bottom: 0

.contact__card
    height: 430px
    display: block
    text-decoration: none
    position: relative
    display: flex
    flex-direction: column
    justify-content: flex-end
    padding: 45px 40px
    &::before
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        left: 0
        top: 0
        background: rgba(black, 0.4)
        z-index: 1
        transition: 0.2s
    &:hover
        &::before
            background: rgba($colorblue, 0.4)
    @include r(1023px)
        height: 300px
        padding: 30px

.contact__img
    position: absolute
    left: 0
    top: 0
    width: 100%
    height: 100%
    object-fit: cover
    display: block

.contact__title
    margin-bottom: 0
    font-weight: bold
    color: white
    position: relative
    flex: none
    z-index: 2
    &::before
        content: ''
        display: block
        width: 57px
        height: 6px
        background: $colorblue
        position: absolute
        top: -15px
        left: 0
    @include r(1023px)
        &::before
            width: 35px
            height: 4px
            top: -10px