.navbottom
    flex: none
    display: flex
    align-items: center
    font-size: 14px
    line-height: 20px
    margin: 0
    padding: 0
    list-style: none
    @include r(767px)
        font-size: 12px
        line-height: 18px
        margin-bottom: 10px

.navbottom__item
    padding: 0 16px
    position: relative
    &::before
        content: ''
        display: block
        width: 1px
        height: 15px
        background: $colorborder
        position: absolute
        right: 0
        top: 50%
        transform: translateY(-50%)
    &:last-child
        padding-right: 0
        &::before
            content: none
    @include r(767px)
        padding: 0 8px
        &:first-child
            padding-left: 0
        &:last-child
            padding-right: 0
        &::before
            height: 10px

.navbottom__link
    color: $colortxtgreylight
    text-decoration: none
    white-space: nowrap
    &:hover
        color: $colortxt