.section-general
    padding: 186px 0 80px
    position: relative
    overflow: hidden
    &::before
        content: ''
        display: block
        width: calc(50% + 300px)
        height: 100%
        background: $colorbggrey
        position: absolute
        right: 0
        top: 0
        opacity: 0
        transform: translateX(100vw)
        transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    &.is-animated::before
        opacity: 1
        transform: none

    @include r(1199px)
        padding: 120px 0 50px
    @include r(1023px)
        padding: 50px 0 0
        &::before
            width: 100%

.section-general__container
    display: flex
    justify-content: space-between
    align-items: flex-start
    position: relative
    @include r(1023px)
        display: block

.section-general__img-box
    order: -1
    width: 624px
    height: 495px
    flex: none
    margin-right: 50px
    overflow: hidden
    @include r(1399px)
        width: 50%
    @include r(1199px)
        margin-left: -30px
    @include r(1023px)
        height: auto
        margin-left: -24px
        margin-right: -24px
        width: auto
    @include r(767px)
        margin-left: -16px
        margin-right: -16px

.section-general__img
    width: 100%
    height: 100%
    object-fit: cover
    display: block
    opacity: 0
    transform: scale(1.2)
    transition: opacity 1.5s $transitiondefault, transform 2.8s linear
    transition-delay: 0.1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        height: auto

.section-general__content
    width: 520px
    @include r(1023px)
        width: auto
        margin-bottom: 40px
    @include r(767px)
        margin-bottom: 30px

.section-general__title
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1023px)
        max-width: 500px
        width: 100%

.section-general__descr
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1.1s
    .is-animated &
        opacity: 1
        transform: none
