.author
    display: flex
    align-items: flex-start
    @include r(1239px)
        position: relative

.author__left
    font-size: 16px
    line-height: 24px
    text-align: right
    margin-right: 15px
    @include r(1239px)
        text-align: left
        margin-right: 0
        margin-left: 15px

.author__right
    flex: none
    display: flex
    flex-direction: column
    align-items: center
    padding-top: 3px
    @include r(1239px)
        order: -1
        display: block

.author__txt
    margin-bottom: 0
    display: block

.author__time
    color: $colortxtgreylight
    font-size: 14px
    line-height: 20px
    display: block

.author__img
    display: block
    width: 40px
    height: 40px
    border-radius: 50%
    object-fit: cover
    margin-bottom: 20px
    @include r(1239px)
        margin-bottom: 0

.author__share
    &:hover
        @include svg-image-icon-share-hover
    @include r(1239px)
        position: absolute
        left: 100%
        top: 5px
        margin-left: 30px



