@import "./normalize"
@import "./vars"
@import "./mixins"
@import "./fonts"
@import "./sprite_generated"

// From libs:
@import "../../libs/slick-carousel/slick/slick.scss"
@import "../../libs/magnific-popup/src/css/main"

html
    height: 100%
    min-width: 360px
    box-sizing: border-box

*, *::before, *::after
    box-sizing: inherit

*
    min-height: 0
    min-width: 0

body
    font: $fontbody
    background: white
    color: $colortxt
    width: 100%
    height: 100%
    &.is-fixed
        position: fixed
    @include r(1023px)
        font-size: 16px
        line-height: 24px
    @include r(767px)
        font-size: 14px
        line-height: 20px

a
    color: inherit
    transition: 0.2s
    &:hover
        text-decoration: none
    &.colorlink
        color: $colorblue
        text-decoration: none
        &:hover
            text-decoration: underline

p
    margin: 0 0 20px

h1
    font: bold 36px/46px $fontmont
    margin: 0 0 40px
    color: inherit
    @include r(1023px)
        font-size: 30px
        line-height: 40px
        margin-bottom: 30px
    @include r(767px)
        font-size: 24px
        line-height: 34px

h2
    font: bold 36px/46px $fontmont
    margin: 0 0 40px
    color: inherit
    @include r(1023px)
        font-size: 30px
        line-height: 40px
        margin-bottom: 30px
    @include r(767px)
        font-size: 24px
        line-height: 34px

h3
    font: bold 24px/34px $fontmont
    margin: 0 0 20px
    color: inherit
    @include r(1023px)
        font-size: 20px
        line-height: 30px
    @include r(767px)
        font-size: 18px
        line-height: 28px

h4
    font: bold 18px/28px $fontmont
    margin: 0 0 20px
    color: inherit
    @include r(767px)
        font-size: 16px
        line-height: 24px

h5
    font: bold 18px/28px $fontdefault
    margin: 0 0 10px
    color: inherit
    @include r(767px)
        font-size: 16px
        line-height: 24px

input[type=text], input[type=email], input[type=tel], input[type=password]
    -webkit-appearance: none
    appearance: none
    display: block
    width: 100%
    background: white
    border-radius: 3px
    border: 1px solid $colorborder
    height: 55px
    font: normal normal 16px/23px $fontdefault
    color: $colortxt
    padding: 15px
    &:focus
        outline: 0
        border-color: $colorblue

input[type=search]
    -webkit-appearance: none
    appearance: none
    display: block
    width: 100%

input[type=text]::-ms-clear,
input[type=email]::-ms-clear,
input[type=tel]::-ms-clear,
input[type=password]::-ms-clear
    display: none
    width: 0
    height: 0

input[type=text]::-ms-reveal,
input[type=email]::-ms-reveal,
input[type=tel]::-ms-reveal,
input[type=password]::-ms-reveal
    display: none
    width: 0
    height: 0

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration
    display: none

textarea
    -webkit-appearance: none
    appearance: none
    display: block
    width: 100%
    background: white
    border-radius: 3px
    border: 1px solid $colorborder
    height: 110px
    font: normal normal 16px/24px $fontdefault
    color: $colortxt
    padding: 10px 15px
    resize: none
    &:focus
        outline: 0
        border-color: $colorblue

// ::-webkit-input-placeholder
// ::placeholder

button
    cursor: pointer

[class^="svg-image"], [class*=" svg-image"]
    display: block

.visuallyhidden
    position: absolute
    width: 1px
    height: 1px
    margin: -1px
    border: 0
    padding: 0
    white-space: nowrap
    clip-path: inset(100%)
    clip: rect(0 0 0 0)
    overflow: hidden

.no-mb
    margin-bottom: 0 !important

.sm-mb
    margin-bottom: 10px

.wrapper
    display: flex
    flex-direction: column
    min-height: 100%

.container
    width: 100%
    max-width: 1440px
    padding: 0 80px
    margin: 0 auto
    @include r(1399px)
        padding: 0 30px
    @include r(1023px)
        padding: 0 24px
    @include r(767px)
        padding: 0 16px

.container-narrow
    max-width: 844px

.container-half
    max-width: 720px
    width: 100%
    padding: 0 80px
    @include r(1399px)
        padding: 0 30px
    @include r(1023px)
        padding: 0 24px
    @include r(767px)
        padding: 0 16px

.title-fadein
    opacity: 0
    transition: opacity 1.5s $transitiondefault
    transition-delay: 0.1s
    .is-animated &
        opacity: 1

.txt-big
    font-size: 24px
    line-height: 34px
    @include r(1023px)
        font-size: 18px
        line-height: 28px
    @include r(767px)
        font-size: 16px
        line-height: 24px

.txt-centered
    text-align: center

.txt-highlighted
    color: $colorblue
    font-weight: bold
    padding: 5px 1.9em
    @include r(1023px)
        padding: 0 2.2em

.img-fullwidth
    width: 100%
    display: block
    @include r(767px)
        margin-left: -16px
        width: calc(100% + 32px)
