.cta {
    position: fixed;
    right: 0;
    // top: 775px;
    top: 75vh;
    width: 55px;
    height: 145px;
    z-index: 900;
    background: $colorgreenlight;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px 0 0 5px;

    &:hover {
        transform: translateX(5px) !important;
    }

    & svg {
        position: relative;
        top: 25px;
        margin-top: 10px;
    }

    @include r(1023px) {
        top: 673px;
    }

    @include r(767px) {
        top: 575px;
        width: 35px;
        height: 135px;
    }
}

.cta__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10000;
}

.cta__text {
    & span {
        display: inline-block;
        min-width: 80px;
        transform: rotate(-90deg);
        font-family: $fontdefault;
        font-weight: 700;
        font-size: 18px;
        line-height: 22px;
        color: $colortxt;
    }
}
