.slider-thumbs
    border-top: 1px solid $colorborder

.slider-thumbs__slide
    position: relative
    cursor: pointer
    &::before
        content: ''
        display: block
        width: 0
        height: 3px
        background: $colorblue
        position: absolute
        left: 0
        top: 0
        z-index: 5
        transition: 0.2s

.slider-thumbs__slide.is-current
    &::before
        width: 100%

    .slider-thumbs__inner
        box-shadow: 0px 4px 20px rgba(131, 131, 131, 0.15)
        border-bottom-color: white
        border-top: 3px solid #D9F1FD
        &::before
            opacity: 1
        &::after
            transform: rotate(180deg)
        @include r(1023px) 
            min-height: 0

    .slider-thumbs__descr
        height: auto
        opacity: 1
        margin-top: 10px
        top: 0

.slider-thumbs__inner
    padding: 25px 70px 25px 30px
    border-bottom: 1px solid $colorborder
    border-top: 3px solid white
    position: relative
    &::before
        content: ''
        display: block
        width: 100%
        height: 1px
        background: white
        position: absolute
        top: -4px
        left: 0
        opacity: 0
    &::after 
        content: ''
        display: block
        width: 20px
        height: 12px
        background: url(../images/arrow.svg) no-repeat center / 20px auto
        position: absolute
        right: 30px
        top: 30px
        transition: 0.2s
    @include r(1199px) 
        padding-right: 30px
    @include r(1023px)
        padding-right: 70px
        

.slider-thumbs__title
    margin-bottom: 0
    @include r(1199px) 
        padding-right: 30px
    @include r(1023px)
        padding-right: 0
    
.slider-thumbs__descr
    height: 0
    opacity: 0
    margin-bottom: 0
    overflow: hidden
    transition: opacity 0.5s, top 0.5s ease-out
    position: relative
    top: -10px
    