.section-usp
    overflow: hidden

.section-usp__heading
    text-align: center
    color: $colorblue
    width: 100%
    max-width: 625px
    margin: 0 auto 90px
    font-size: 36px
    line-height: 46px
    font-weight: bold
    @include r(1023px)
        font-size: 30px
        line-height: 40px
        margin-bottom: 60px
        max-width: 500px
    @include r(767px)
        font-size: 24px
        line-height: 34px
        margin-bottom: 30px
        max-width: 400px

.section-usp__heading-word
    display: inline-block
    opacity: 0
    transform: translate(0, 101%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    .is-animated &
        opacity: 1
        transform: none
    &:first-child
        transition-delay: 0.1s
    &:nth-of-type(2)
        transition-delay: 0.2s
    &:nth-of-type(3)
        transition-delay: 0.3s
    &:nth-of-type(4)
        transition-delay: 0.4s
    &:nth-of-type(5)
        transition-delay: 0.5s
    &:nth-of-type(6)
        transition-delay: 0.6s
    &:nth-of-type(7)
        transition-delay: 0.7s
    &:nth-of-type(8)
        transition-delay: 0.9s
    &:nth-of-type(9)
        transition-delay: 1s
    &:nth-of-type(10)
        transition-delay: 1.1s
    &:nth-of-type(11)
        transition-delay: 1.2s

.section-usp__cols
    display: flex
    justify-content: space-between
    align-items: center
    @include r(1023px)
        display: block

.section-usp__left
    width: 100%
    max-width: 545px
    @include r(1023px)
        margin: 0 auto 30px
        text-align: center

.section-usp__right
    width: calc(50% + 80px - 15px)
    margin-right: -80px
    margin-left: 50px
    background: $colorblue
    color: white
    padding: 160px 80px
    flex: none
    opacity: 0
    transform: translateX(100vw)
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1.6s
    .is-animated &
        opacity: 1
        transform: none
    @include r(1399px)
        width: 50%
        margin-right: -30px
    @include r(1199px)
        padding: 100px 50px
    @include r(1023px)
        margin-right: -24px
        margin-left: -24px
        width: auto
    @include r(767px)
        margin-right: -16px
        margin-left: -16px
        padding: 50px 16px

.section-usp__highlighted
    font-size: 36px
    line-height: 46px
    margin-bottom: 0
    @include r(1023px)
        font-size: 30px
        line-height: 40px
        max-width: 545px
        width: 100%
        margin: 0 auto
        text-align: center
    @include r(767px)
        font-size: 24px
        line-height: 34px

.section-usp__title
    margin-bottom: 20px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1.5s
    .is-animated &
        opacity: 1
        transform: none

.section-usp__descr
    margin-bottom: 30px
    opacity: 0
    transform: translate(0, 50%)
    transform-origin: 50% 100%
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault
    transition-delay: 1.6s
    .is-animated &
        opacity: 1
        transform: none

.section-usp__list
    column-count: 2
    column-gap: 120px
    li 
        break-inside: avoid
        margin-bottom: 0
        padding-bottom: 20px
        opacity: 0
        transition: opacity 1.5s $transitiondefault
        transition-delay: 2s
        .is-animated &
            opacity: 1
            transform: none
        &:nth-child(2)
            transition-delay: 2.2s
        &:nth-child(3)
            transition-delay: 2.4s
        &:nth-child(4)
            transition-delay: 2.6s
        &:nth-child(5)
            transition-delay: 2.8s
        &:nth-child(6)
            transition-delay: 3s
        &:nth-child(7)
            transition-delay: 3.2s

    @include r(1399px)
        column-gap: 80px
    @include r(1023px)
        text-align: left
    @include r(767px)
        column-gap: 30px