.jobpanel
    display: flex
    @include r(1023px)
        margin-left: -24px
        margin-right: -24px
    @include r(767px)
        flex-direction: column
        margin-left: -16px
        margin-right: -16px

.jobpanel__left
    width: 48.5%
    flex: none
    margin-right: 30px
    position: relative
    transform: translate(-50%, 0)
    transition: transform 2s $transitiondefault
    transition-delay: 0.7s
    .is-animated &
        transform: none

    @include r(1023px)
        width: 50%
        margin-right: 0
    @include r(767px)
        width: 100%

.jobpanel__right
    flex-grow: 1
    margin-right: -80px
    background: $colorbggrey
    padding: 80px 110px
    @include r(1399px)
        margin-right: -30px
    @include r(1199px)
        padding: 60px 
    @include r(1023px)
        margin-right: 0
    @include r(767px)
        order: -1
        padding: 30px 16px
        text-align: center

.jobpanel__img
    width: 100%
    height: 100%
    display: block
    object-fit: cover
    position: absolute
    left: 0
    top: 0
    @include r(767px)
        position: static

.jobpanel__descr
    width: 100%
    max-width: 370px
    margin-bottom: 30px
    @include r(767px)
        margin-left: auto
        margin-right: auto
