.section-hero
    overflow: hidden
    @include r(767px)
        margin-bottom: 50px

.section-hero__title
    position: relative
    margin-bottom: 65px
    margin-left: 80px
    opacity: 0
    transform: translateX(-100vw)
    transition: opacity 1.5s $transitiondefault, transform 1.2s $transitiondefault
    .is-animated &
        opacity: 1
        transform: none
    &::before
        content: ''
        display: block
        width: 370px
        height: 9px
        background: $colorgreen
        position: absolute
        right: 100%
        margin-right: 30px
        top: 20px
        transform: translateX(-100vw)
        transition: transform 1.7s $transitiondefault
        .is-animated &
            transform: none
    @include r(767px)
        margin-left: 30px
        margin-bottom: 20px
        &::before
            height: 5px
            margin-right: 15px
            top: 16px

.section-hero__img-box
    width: 100%
    max-width: 2560px
    height: 800px
    margin: 0 auto
    overflow: hidden

    @include r(1920px)
        height: 575px
    @include r(1399px)
        height: 500px
    @include r(1199px)
        height: 400px
    @include r(1023px)
        height: 300px
    // @include r(767px)
    //     height: 200px

.section-hero__img
    display: block
    width: 100%
    height: 100%
    object-fit: cover
    transform: scale(1.2)
    transition: transform 2.8s linear
    .is-animated &
        transform: none
