.ul
    list-style: none
    @include r(767px)
        padding: 0 0 0 0.5em
    & > li
        margin-bottom: 20px
        position: relative
        padding-left: 1.5em
        margin-left: -1.5em
        &::before
            content: "\25A0"
            color: $colorblue
            display: block
            font-size: 10px
            transform: rotate(-45deg)
            position: absolute
            left: 0
            top: 0
        @include r(767px)
            margin-left: 0
    ul, ol
        margin-top: 20px
        margin-bottom: 20px
    ul
        @include r(767px)
            padding-left: 0