.solutions {
    margin-bottom: 60px;
    @include r(1023px) {
        position: relative;
        z-index: 10;
        margin-bottom: 0;
    }
}

.solutions__title {
    max-width: fit-content;
    margin: 0 auto 30px;
    opacity: 0;
    transform: translate(0, 101%);
    transform-origin: 50% 100%;
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault;
    transition-delay: 0.5s;

    @include r(575px) {
        margin-left: 0;
        margin-bottom: 20px;
    }
}

.is-animated .solutions__title {
    opacity: 1;
    transform: none;
}

.solutions__wrapper {
    position: relative;
    z-index: 10;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 25px 31px;

    @include r(1023px) {
        grid-template-columns: repeat(1, 1fr);
        gap: 25px 0;
    }
}

.solutions__item {
    background-color: white;
    display: flex;
    flex-direction: column;
    padding: 30px 30px 59px 30px;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.06);
    border-radius: 3px;
    opacity: 0;
    transform: translate(0, 101%);
    transform-origin: 50% 100%;
    transition: opacity 1.5s $transitiondefault, transform 1s $transitiondefault;
    transition-delay: 1s;

    @include r(1023px) {
        display: grid;
        grid-template-columns: 165px 1fr;
        gap: 0 32px;
        padding: 30px 30px 40px 30px;
    }

    @include r(767px) {
        grid-template-columns: 118px 1fr;
    }

    @include r(575px) {
        display: block;
        padding-bottom: 45px;
    }
}

.is-animated .solutions__item {
    opacity: 1;
    transform: none;
}

.solutions__icon {
    width: 165px;
    height: 140px;
    margin-bottom: 25px;

    span {
        display: block;
        width: 165px;
        height: 140px;
    }

    @include r(767px) {
        width: 118px;
        height: 100px;
        margin-bottom: 30px;

        span {
            width: 118px;
            height: 100px;
        }
    }
}

.solutions__description {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.solutions__header {
    width: 100%;
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 26px;
}

.solutions__text {
    width: 100%;
    margin-bottom: 40px;
    flex-grow: 1;

    @include r(575px) {
        margin-bottom: 20px;
    }
}

.solutions__btn {
    align-self: flex-start;
}
