.founder {
    gap: 159px;

    .section-leader__left {
        order: 2;
    }

    .section-leader__right {
        order: 1;
    }

    @include r(1239px) {
        gap: 80px;
    }

    @include r(1023px) {
        gap: 60px;
    }
}

.section-leader__left {
    padding-right: 0;
}

.president {
    .president__title {
        opacity: 0;
        visibility: hidden;

        @include r(767px) {
            display: none;
        }
    }
}

.team__wrapper {
    max-width: 1170px;
    display: grid;
    grid-template-columns: repeat(3, 352px);
    gap: 0 57px;
    justify-content: center;

    margin: 0 auto;

    @include r(1239px) {
        grid-template-columns: repeat(3, 300px);
        gap: 0 35px;
    }

    @include r(1023px) {
        grid-template-columns: repeat(3, 1fr);
        gap: 0 30px;
    }

    @include r(575px) {
        grid-template-columns: repeat(1, 280px);
        gap: 30px 0;
    }

    @include r(350px) {
        grid-template-columns: repeat(1, 260px);
        gap: 20px 0;
        justify-content: flex-start;
    }
}

.team__image {
    position: relative;
    margin-bottom: 20px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.team__name {
    margin-bottom: 5px;
    font-family: $fontmont;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: $colortxt;
}

.team__position {
    font-family: $fontmont;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: $colortxt;
}

.leader__title_small {
    display: none;

    @include r(767px) {
        display: block;
    }
}

.leader__title_wide {
    position: relative;
    top: -40px;
    left: -55px;
    display: block;

    @include r(767px) {
        display: none;
    }
}

.founder__descr {
    padding-left: 55px;

    @include r(575px) {
        padding-left: 0;
    }
}

.section__president {
    padding-left: 55px;

    @include r(767px) {
        padding-left: 0;
    }
}

.president__descr {
    @include r(767px) {
        padding-left: 55px;
    }

    @include r(575px) {
        padding-left: 0;
    }
}
