.insights
    @include r(767px)
        display: flex
        margin-left: -16px
        height: 100%

.insights__item
    text-decoration: none
    display: block
    position: relative
    box-shadow: 0px 5px 20px rgba(55, 55, 55, 0.09)
    margin-bottom: 44px
    &:hover
        .insights__title
            color: $colorblue
        .insights__more::after
            width: 100%
            
    @include r(767px)
        flex: none
        width: 258px
        margin-left: 16px
        margin-bottom: 20px

.insights__item_decor
    &::before
        content: ''
        display: block
        width: 218px
        height: 139px
        background: $colorgreen
        position: absolute
        left: -30px
        bottom: -35px
        z-index: -1
    @include r(1023px)
        &::before
            width: 120px
            height: 140px
            left: -24px
            bottom: -24px
    @include r(767px)
        &::before
            content: none

.insights__img-box
    position: relative
    height: 505px
    overflow: hidden
    @include r(1023px)
        height: 250px
    
.insights__img
    display: block
    width: 100%
    height: 100%
    object-fit: cover
    position: absolute
    top: 0
    left: 0
    transform: scale(1.2)
    transition: transform 3.8s linear
    .is-animated &
        transform: none

.insights__content
    background: white
    position: absolute
    right: 0
    bottom: 0
    padding: 35px 30px
    width: 387px
    z-index: 1
    @include r(1023px)
        width: 100%
        position: static
        padding: 28px 30px 25px
    @include r(767px)
        padding: 25px 16px

.insights__subtitle
    font-size: 16px
    line-height: 24px
    font-weight: bold
    text-transform: uppercase
    color: $colorblue
    margin-bottom: 7px
    @include r(767px)
        font-size: 14px
        line-height: 20px
        margin-bottom: 17px

.insights__title
    margin-bottom: 10px
    transition: 0.2s

.insights__descr
    font-size: 16px
    line-height: 20px
    margin-bottom: 15px
    @include r(767px)
        font-size: 14px
        line-height: 20px
        margin-bottom: 20px
